<template>
    <div class="discount">
    	<div class="discount_banner">
    		 <swiper :options="swiperOption">
		        <swiper-slide><img src="../../assets/dis_banner1.png"/></swiper-slide>
		        <swiper-slide><img src="../../assets/dis_banner2.png"/></swiper-slide>
		        <swiper-slide><img src="../../assets/dis_banner3.png"/></swiper-slide>
		        <swiper-slide><img src="../../assets/dis_banner4.png"/></swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    	</div>
    	<div class="justAword" v-if="false">
    		无论何地都有都会可领<p>找优惠</p>
    	</div>
    	<div class="scrollBox" @click="clickAll">
    	<div class="discountCoupon">
    		<div class="top">
    			<div>优惠券</div>
    			<div class="moreCoupon" @click="to_moreCoupon"> 更多  ＞ </div>
    		</div>
    		<div class="goods">
    			  <swiper :options="swiperOption2" class="swiperbox">
			        <swiper-slide class="carts" v-for="data in couponList" :key="data.id">
			        		<div class="goodsLogo" @click="youhui(data)"><img src="../../assets/yhq2.jpg" alt=""></div>
			        		<div class="goodsRight">
			        			<div class="goodsName">{{data.name}}</div>
			        			<div class="goodsFeature">{{data.feature}}</div>
			        			<div class="rightBottom">
			        				<div class="merchantLogo">{{data.merchantLogo}}</div>
			        				<div class="merchantName">{{data.merchantName}}</div>
			        				<div class="people">{{data.people}}人领取</div>
			        			</div>
			        		</div>
			        </swiper-slide>
			      </swiper>
    		</div>
    	</div>
    	<div class="discountCoupon">
    		<div class="top">
    			<div>免费课程</div>
    			<div class="moreCoupon" @click="to_freeLesson"> 更多  ＞ </div>
    		</div>
    		<div class="goods">
    			  <swiper :options="swiperOption2" class="swiperbox">
			        <swiper-slide class="carts" v-for="data in couponList" :key="data.id">
			        		<div class="goodsLogo" @click="free(data)"><img src="../../assets/lesson.png" alt=""></div>
			        		<div class="goodsRight">
			        			<div class="goodsName">{{data.name}}</div>
			        			<div class="goodsFeature">{{data.feature}}</div>
			        			<div class="rightBottom">
			        				<div class="merchantLogo">{{data.merchantLogo}}</div>
			        				<div class="merchantName">{{data.merchantName}}</div>
			        				<div class="people">{{data.people}}人领取</div>
			        			</div>
			        		</div>
			        </swiper-slide>
			      </swiper>
    		</div>
    	</div>
    	<div class="discountCoupon">
    		<div class="top">
    			<div>0元购</div>
    			<div class="moreCoupon" @click="to_zeroBuy"> 更多  ＞ </div>
    		</div>
    		<div class="goods">
    			  <swiper :options="swiperOption2" class="swiperbox">
			        <swiper-slide class="carts" v-for="data in couponList" :key="data.id">
			        		<div class="goodsLogo" @click="Oyuan(data)"><img src="../../assets/0yuan.jpg" alt=""></div>
			        		<div class="goodsRight">
			        			<div class="goodsName">{{data.name}}</div>
			        			<div class="goodsFeature">{{data.feature}}</div>
			        			<div class="rightBottom">
			        				<div class="merchantLogo">{{data.merchantLogo}}</div>
			        				<div class="merchantName">{{data.merchantName}}</div>
			        				<div class="people">{{data.people}}人领取</div>
			        			</div>
			        		</div>
			        </swiper-slide>
			      </swiper>
    		</div>
    	</div>
    	<div class="discountCouponShop">
    		<div class="top">
    			<div>优惠商城</div>
    			<div class="moreCoupon" @click="to_DiscountMall"> 更多  ＞ </div>
    		</div>
    		<div class="shopgoods">
	    		<swiper :options="swiperOption3" class="swiperbox">
				  <swiper-slide class="carts" v-for="data in couponList" :key="data.id">
				  		<div class="content_shop">
			                    <div class="style" @click="shop(data)"><img src="../../assets/0yuan2.jpg" alt=""></div>
			                    <div class="name">商品名商品名商品名商品名商品名</div>
			                    <div class="price">到手价￥4999.21</div>
			                    <div class="old_price">原价￥5000</div>
			                    <div class="btm">
		                        <div class="logo"></div>
		                        <div class="merchant">商家名称</div>
		                        <div class="num">2000人购买</div>
		                    </div>
		                </div>
					</swiper-slide>
				</swiper>
			</div>
    	</div>
    	</div>
    </div>
</template>

<script>
	import 'swiper/dist/css/swiper.css'
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
    export default {
    	data(){
    		return{
    			   swiperOption: {
    			   			autoplay:{
    			   				autoplay:true
    			   			},
				          pagination: {
				            el: '.swiper-pagination',
				            dynamicBullets: true
				          }
				       },
				       swiperOption2:{				       	
				          slidesPerView: 1.2,
				          spaceBetween: 30,
				          freeMode: true
				       },
				       //优惠商城的配置， 真的是妖艳
				       swiperOption3:{
				       	slidesPerView: 1.6,
				          spaceBetween: 30,
				          freeMode: true
				       },
    			   couponList:[
                    {logo:"/static/img/yhq2.c571452.jpg",name:"优惠券名称优惠券名称优惠券名称优惠券名称",feature:"优惠券特点",merchantLogo:"",merchantName:"商家名称",people:"115万",id:"1"},
                    {logo:"/static/img/yhq2.c571452.jpg",name:"优惠券名称优惠券名称优惠券名称优惠券名称",feature:"优惠券特点",merchantLogo:"",merchantName:"商家名称",people:"111万",id:"2"},
                    {logo:"/static/img/yhq2.c571452.jpg",name:"优惠券名称优惠券名称优惠券名称优惠券名称",feature:"优惠券特点",merchantLogo:"",merchantName:"商家名称",people:"211万",id:"3"},
                    {logo:"/static/img/yhq2.c571452.jpg",name:"优惠券名称优惠券名称优惠券名称优惠券名称",feature:"优惠券特点",merchantLogo:"",merchantName:"商家名称",people:"113万",id:"4"},
                    {logo:"/static/img/yhq2.c571452.jpg",name:"优惠券名称优惠券名称优惠券名称优惠券名称",feature:"优惠券特点",merchantLogo:"",merchantName:"商家名称",people:"121万",id:"5"}
                    ],
                datalist:[{id:1},{id:2},{id:3},{id:4}]
                    
    		}
    	},
    	methods:{
    		to_moreCoupon(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')   				
    			}else{
					//跳转到优惠券列表页
				this.$router.push('/discount/ticket')
    			}		
    		},
    		to_freeLesson(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')   				
    			}else{
					//跳转到优惠券列表页
				this.$router.push('/discount/freeclass')
    			}		
    		},
    		to_zeroBuy(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')   				
    			}else{
					//跳转到优惠券列表页
				this.$router.push('/discount/buy')
    			}		
    		},
    		to_DiscountMall(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')   				
    			}else{
					//跳转到优惠券列表页
				this.$router.push('/discount/shop')
    			}		

    		},
    		clickAll(){
    			if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}
    		},
			//跳第三方
			youhui(data){
				if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}else{
    			this.$router.push({
                    path:'/third',
     
     query:{
                        title:"优惠券",
                        url:"http://touch.podinns.com/mobile/buycard/20180417"
                    }
                })
    			}
                
			},
			free(data){
				if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}else{
    				this.$router.push({
                    path:'/third',
                    query:{
                        title:"免费课程",
                        url:"http://m.fudaojun.com/yingxiao/common.html?channel=wenjuanxing3"
                    }
                })
    			}
				
			},
			Oyuan(data){
				if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}else{
    				this.$router.push({
                    path:'/third',
                    query:{
                        title:"0元购",
                        url:"https://vipkid-bd.mikecrm.com/y6E2eoV"
                    }
                })
    			}
                
			},
			shop(data){
				if(!localStorage.getItem("userId")){
    				this.$router.push('/login')
    			}else{
    				this.$router.push({
                    path:'/third',
                    query:{
                        title:"优惠商城",
                        url:"http://touch.podinns.com/mobile/buycard/20180417"
                    }
                })
    			}
				
			}
   },
      components:{swiper,swiperSlide},
    }
</script>

<style lang="scss" scoped>
   .discount{
   	width: 100%;
   	height: 100%;
   	overflow: hidden;
   	background: #f2f2f2;
   	display: flex;
   	flex-direction: column;
	.discount_banner{
		width: 100%;
		height: 7.5rem;
		img{
			width: 100%;
			height: 100%;
		}
	}
	.justAword{
		width: 100%;
		height: 5.75rem;
		padding-top: 2rem;
		text-align: center;
		font-size: 0.75rem;
		color: #000000;
		background: #fae44c;
		p{
			margin-top: 0.31rem;
		}
	}
	.scrollBox{
		width: 102%;
		flex: 1;
		padding-right: 2%;
		overflow-x: hidden;
   		overflow-y: scroll;
   		margin-top: .5125rem;
	}
	.discountCoupon{
		width: 100%;
		height: 8.03rem;		
		.top{
			width: 100%;
			height: 1.75rem;
			background: #d5d5d5;
			display: flex;	
			padding: 0 0.62rem;
			justify-content: space-between;
			font-size: 0.9rem;
			color: #000000;
			align-items: center;
			}
			.goods{
				width: 100%;
				height: 6.25rem;
				padding-left: 0.62rem;
				padding-top: 0.93rem;
				background: #f2f2f2;
				.swiperbox{
					width: 100%;
					height: 4.46rem;
					.carts{
						width: 16.65rem;
						height: 100%;
						display: flex;
						.goodsLogo{
							width: 4.46rem;
							height: 4.46rem;
							
							text-align: center;
							line-height: 4.46rem;
							img{height: 100%;width: 100%;display: inline-block;}
						}
						.goodsRight{
						flex: 1;
						height: 100%;
						padding-left: 0.62rem;
						.goodsName{
							width: 9.68rem;
							height: 2.18rem;
							color: #000000;
							font-size: 0.75rem;
							line-height: 0.93rem;
							padding-top: 0.31rem;
						}
						.goodsFeature{
							width: 5.43rem;
							height: 0.87rem;
							background: #dcdcdc;
							color: #000000;
							font-size: 0.75rem;
							text-align: center;line-height: 0.87rem;
							margin-top: 0.15rem;
							margin-bottom: 0.25rem;
						}
						.rightBottom{
							width: 100%;
							height: 1.06rem;
							display: flex;
							align-items: center;
							color: #000000;
							font-size: 0.62rem;
							.merchantLogo{
								width: 0.78rem;
								height: 0.78rem;
								border: 1px solid #bfbfbf;
							}
							.merchantName{
								margin-left: 0.62rem;								
							}
							.people{
								margin-left:0.43rem;
							}
						}
						}
					}
				}
			}
	}
	.discountCouponShop{
		width: 100%;
		.top{
			width: 100%;
			height: 1.75rem;
			background: #d5d5d5;
			display: flex;	
			padding: 0 0.62rem;
			justify-content: space-between;
			font-size: 0.9rem;
			color: #000000;
			align-items: center;
			}
			.shopgoods{				
          		 width: 100%;
				height: 18.25rem;
				padding-left: 0.62rem;
				padding-top: 0.93rem;
				background: #f2f2f2;
				.swiperbox{
					width: 100%;
					height:100%;
					.carts{
						
						.content_shop{
                    width: 80%;height: 100%;padding-top:1rem;border-bottom:.03125rem solid #bfbfbf;
                    .style{height: 9.40625rem;width: 9.40625rem;
						img{height: 100%;width: 100%;display: inline-block;}
						}
                    .name{width: 87%;height: 1.5625rem;line-height: 1.5625rem;overflow: hidden;
                            text-overflow: ellipsis;white-space: nowrap;color: #010101;font-size: .84375rem;}
                    .price{height: 1.125rem;line-height: 1.125rem;color: #ff0000;}
                    .old_price{height: 1.71875rem;line-height: 1.71875rem;color: #9f9f9f;font-size: .6875rem;text-decoration: line-through;}
                    .btm{
                        width: 100%;height: 1.0625rem;line-height: 1.0625rem;font-size: .625rem;
                        .logo{height: 1.03125rem;width: 1.03125rem;border:.03125rem solid #000;float: left;}
                        .merchant{float: left;margin-left: .3125rem;}
                        .num{float: right;}
                        }
                		}
					}
				}
          
        
			}
	}
   }
</style>



