<template>
	<div class="getCard">
		<div class="form_header">
			<div class="back" @click="back"></div>
			<div class="center">办卡任务</div>
			<div class="block"></div>
		</div>
		<div class="order">
			<div class="order1">完成任务</div>
			<div class="order2">审核通过</div>
			<div class="order3">获得奖励</div>
		</div>
		<div class="mession_tab">
			<div :class="[isOk2?'mession_list':'mession_list2']" @click="to_messionlist">
				<div class="aways">任务列表</div>
				<div class="select_div" v-show="isLimit">
					<select>
						<option v-for="sel in sels" v-bind:value="sel.value" :key="sel.value">{{sel.order}}</option>					
					</select>
					<select>
						<option v-for="sel2 in sels2" v-bind:value="sel2.value" :key="sel2.value">{{sel2.order}}</option>					
					</select>
				</div>
			</div>
			<div :class="[isOk?'my_mession':'my_mession2']" @click="to_mymession"><span>办卡进度</span></div>
		</div>
		<router-view></router-view>
	</div>
</template>

<script>
	export default{
		name:"",
		data(){
			return{
				sels:[
					{value:"1",order:"种类不限"},
					{value:"2",order:"购物卡"},
					{value:"3",order:"车主卡"},
					{value:"4",order:"商旅卡"},
					{value:"5",order:"女性卡"},
					{value:"6",order:"休闲卡"},
					{value:"7",order:"金融卡"},
					{value:"8",order:"学生卡"},
					
				],
					sels2:[
					{value:"9",order:"金额不限"},
					{value:"10",order:"金额    ↑"},
					{value:"11",order:"金额    ↓"}
					
				],
				isOk:true,
				isOk2:true,
				isLimit:true
					}
			
		},
		methods:{
			back(){
				this.$router.push("/makemoney/banka")
			},
			to_mymession(){
				this.isOk = false,
				this.isLimit=false	,
				this.isOk2=false,
				this.$router.push('/makemoney/banka/getcardmission/myGetcardMission')

			},
			to_messionlist(){
				this.isOk = true,
				this.isLimit=true,
				this.isOk2=true,
				this.$router.push('/makemoney/banka/getcardmission/getcardMissionList')
			}
		},
		updated () {
			if(this.$route.name == "GetcardMissionList"){
				this.isOk = true,
				this.isLimit=true,
				this.isOk2=true
			}else{
				this.isOk = false,
				this.isLimit=false,
				this.isOk2=false
			}
		}
	}
</script>

<style lang="scss" scoped>
  .getCard{
  	width: 100%;
  	height: 100%;
  	background: #dadada;
	overflow: hidden;
  
.form_header{
  	width: 100%;
	height: 2.85rem;
  	background: #fae44c;
  	padding-left: 0.62rem;
  	padding-right: 0.62rem;
  	box-sizing: border-box;
  	display: flex;
  	justify-content: space-between;
  	 .back{
  	width: 2.62rem;
  	height: 100%;
  	background: url(../../../../assets/back.png) no-repeat center center;
  	background-size: 0.62rem 1.28rem;
 		 }
 		  .center{
  	width: 7.61rem;
  	height: 2.81rem;
  	text-align: center;
  	line-height: 2.81rem;
  	font-size: 1.12rem;
    color: #72463d;
  }
  .block{
  	width: 2.62rem;
  }
 	 }
 	 
   .order{
   	width: 100%;
   	height: 2.18rem;
   	padding: 0 0.62rem;
   	background: #d5d5d5;
   	display: flex;
   	justify-content: space-between;
   	align-items: center;
   	div{
   		height: 1.21rem;
   		text-align: center;
   		line-height: 1.21rem;
   		color: #ffffff;
   		font-size: 0.68rem;
   	}
   	.order1{
   		width: 4.78rem;
   		background: url(../../../../assets/order1.png) no-repeat center center;
   		background-size: 100% 100%;
   	}
   	.order2{
   		width: 4.75rem;
   		background: url(../../../../assets/order2.png) no-repeat center center;
   		background-size: 100% 100%;
   	}
   	.order3{
   		width: 4.93rem;
   		background: url(../../../../assets/order3.png) no-repeat center center;
   		background-size: 100% 100%;
   	}
   }
   .mession_tab{
   	width: 100%;
   	height: 2.06rem;
   	display: flex;
   	font-size: 0.9rem;
    .mession_list{
    	width: 17.06rem;
    	height: 100%;
    	display: flex;
    	border-right:1px solid #999999;
    	background:#ffffff;
    	.aways{
    		width: 3.87rem;
    		margin-left: 1.43rem;
    		height: 100%;
    		text-align: center;
    		line-height: 2.06rem;
    		color: #f94b4b;
    		border-bottom: 1px solid #f94b4b;
    		
    	}
    	.select_div{
    	flex: 1;
    	height: 100%;
    	display: flex;
    select{
		  /*很关键：将默认的select选择框样式清除*/
			  appearance:none;
			  /*-moz-appearance:none;
			  -webkit-appearance:none;*/
		
			  /*在选择框的最右侧中间显示小箭头图片*/
			
			  background: url(../../../../assets/xiala.png) no-repeat 4.6rem center;
			background-size: 0.68rem 0.4rem;
			
			  /*为下拉小箭头留出一点位置，避免被文字覆盖*/
			 font-size: 0.71rem;
			 display: block;
			  padding-right: 9px;
			  border: none;
			  height: 100%;
			  border: none;
			  padding-left: 0.53rem;
			  line-height: 2.06rem;
			  color: #f94b4b;
			  flex: 1;
			  option{
			  	display: block;
			  	width: 100%;
			  	height: 100%;
			  	text-align: center;
			  }
    	}
    	
/*清除ie的默认选择框样式清除，隐藏下拉箭头*/
select::-ms-expand { display: none; }
}
    }
    .mession_list2{
    	width: 7.28rem;
    	height: 2.06rem;
    	text-align: center;
    	line-height: 2.06rem;
    	color: #999999;
    	background: #ffffff;
    	border-right:1px solid #999999;
    }
    .my_mession{
    	flex: 1;
    	text-align: center;
    	line-height: 2.06rem;
    	background: #ffffff;
    	color: #999999;
    	
    }
    .my_mession2{
    	flex: 1;
    	background: #ffffff;
    	color: #f94b4b;
    	text-align: center;
    	line-height: 2.06rem;
    	span{
    		display: block;
    		width: 4.09rem;
    		height: 2.03rem;
    		margin: 0 auto;
    		border-bottom: 1px solid #f94b4b;
    	}
    }
   }
 }	

</style>



