<template>
    <div class="drawCashRecord">
        <div class="draw_header">
			<div class="back" @click="drawCashRecordBack"></div>
			<div class="center">提现记录</div>
			<div class="block"></div>
		</div>

        <div class="content_record">
            <div class="none" v-if="isNone">暂无提现记录</div>
            <div class="Notnone" v-if="!isNone">
                <div class="head"><span>{{time}}</span>提现总计{{total}}元</div>
                <div class="box_main">

                  <div class="box" v-for="item in itemlist" :key="item.id" @click="btn(item)">
                    <div class="left" :style="{background: 'url('+item.bg+') no-repeat center center'}"></div>
                    <div class="right">
                         <div class="tp">【{{item.way}}】 至<span>{{item.account}}</span><span>{{item.money}}元</span></div>
                         <div class="bm">{{item.time}}<span>{{item.status}}</span></div>
                    </div> 
                  </div>
                </div>
              
            </div>
        </div>

        <div class="fail" v-if="isfail">
            <div class="cha" @click="close">X</div>
            <div class="jd">提现进度</div>
            <div class="sb">失败！</div>
            <div class="reson">原因：{{reson}}</div>
            <div class="time">时间:{{fail_time}}</div>
        </div>
    </div>
</template>

<script>
    //提现记录页面
    export default {
        name:"DrawCashRecord",
        data(){
            return{
                //是否有提现记录
                isNone:"",
                time:"2018年3月",
                total:"0",
                itemlist:[
                          {id:1,bg:require('../../../assets/zhifubao.png'),way:"支付宝提现",account:"支付宝账号",money:"1000.00",time:"3-16 15:59:57",status:"成功"},
                          {id:2,bg:require('../../../assets/weixin.png'),way:"微信提现",account:"微信账号",money:"1000.00",time:"3-16 15:59:57",status:"成功"},
                          {id:3,bg:require('../../../assets/huafei.png'),way:"话费充值",account:"137777xxxx",money:"1000.00",time:"3-16 15:59:57",status:"打款失败"},
                          {id:4,bg:require('../../../assets/zhifubao.png'),way:"支付宝提现",account:"支付宝账号",money:"1000.00",time:"3-16 15:59:57",status:"成功"},
                          {id:5,bg:require('../../../assets/weixin.png'),way:"微信提现",account:"微信账号",money:"1000.00",time:"3-16 15:59:57",status:"成功"},
                          {id:6,bg:require('../../../assets/huafei.png'),way:"话费充值",account:"话费充值",money:"1000.00",time:"3-16 15:59:57",status:"打款失败"}
                         ],
                //失败下的弹窗参数
                isfail:"",
                fail_time:"",
                reson:""
              
            }
        },
        methods: {
            drawCashRecordBack(){ 
                     this.$router.push('/my/drawCash')
                },
            btn(item){
                //若打款失败，弹出弹框 渲染弹框内内容
                if(item.status == "打款失败"){
                    this.isfail = true;
                    this.fail_time = "2018-4-3 15:16:03",
                    this.reson = "用户名错误"
                }
            },
            //关闭失败窗口
            close(){
                this.isfail = false;
            }
        },
        computed:{
           
        },
        beforeMount(){
            this.isNone = false;
        }
    }
</script>

<style lang="scss" scoped>
.drawCashRecord{
   
    height: 100%;
    width: 100%;
    overflow: hidden;
    .fail{
        height: 7.0625rem;width: 11.09375rem;
        border:.03125rem solid #535353;
        position: fixed;top: 40%;
        background: #fff;left: 25%;
        border-radius: .41875rem;color:#000000;
        .cha{position: relative;left: 90%;top:2%;}
        .jd{font-size: .6875rem;width: 100%;text-align: center;}
        .sb{font-size: 1.03125rem;margin-top: .875rem;width: 100%;text-align: center;}
        .reson{margin-top: .46875rem;width: 100%;text-align: center;font-size: .6875rem;}
        .time{margin-top: .46875rem;width: 100%;text-align: center;font-size: .6875rem;}
    }
    .draw_header{
        width: 100%;
	height: 2.85rem;
        background: #fae44c;
        padding-left: 0.62rem;
        padding-right: 0.62rem;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        .back{
            width: 2.62rem;
            height: 100%;
            background: url(../../../assets/back.png) no-repeat left center;
            background-size: 0.62rem 1.28rem;
        }
        .center{
            width: 4.61rem;
            height: 2.81rem;
            text-align: center;
            line-height: 2.81rem;
            font-size: 1.12rem;
            color: #72463d;
           
        }
        .block{
            width: 2.62rem;
        }
    }
    .content_record{
        width: 100%;
        height: 100%;
        overflow: auto;
        .none{
            height: 100%;
            width: 100%;
            text-align: center;
            position: absolute;
            top: 50%;
            color: #040404;
        }
        .Notnone{
            .head{height: 2.34375rem;width: 100%;background: #e1e1e1;
                line-height: 2.34375rem;color: #040404;border-bottom:.03125rem solid #cacaca;
                span{margin-left: .625rem;margin-right: 1.78125rem;}
                }
            .box_main{
                margin: 0 .625rem;
              .box{
                height: 4rem;width: 100%;
                border-bottom: .03125rem solid #aaaaaa;
                display: flex;
                .left{
                    height: 4rem;
                    width: 3.28125rem;
                    text-align: center;
                    margin-right: .75rem;
                    background-size: 1.8rem 2.5rem !important;
                  }
                .right{
                    width: 100%;height: 100%;
                    .tp{margin-top: 1.34375rem;width: 100%;
                        font-size: .875rem;color: #040404;margin-bottom:.75rem;
                        span:nth-child(1){font-size: .6875rem;color: #6b6b6b;}
                        span:nth-child(2){float: right;}
                        }
                    .bm{font-size: .625rem;color: #6b6b6b;width: 100%;span{float: right;}}
                }
              }
            }
           
        }
    }
}
</style>


