<template>
	<div class="login">
		<div class="login_header">
			<div class="back" @click="back"></div>
			<div class="center">修改密码</div>
			<div class="block"></div>
		</div>
		<div class="login_form">
            <div class="phoneNumber">
				<input type="password" name="" id="old_psw" value="" placeholder="请输入旧密码" v-model="old_psw"/>
			</div>
			<div class="phoneNumber">
				<input type="password" name="" id="new_psw" value="" placeholder="设置6-15位字母/数字新密码" v-model="new_psw"/>
			</div>
			<div class="password">
				<input type="password" name="" id="confirm_psw" value="" placeholder="请确认以上的密码" v-model="confirm_psw"/>
			</div>

            <div class="to_register" @click="go_forget">忘记密码？</div>
			<div class="login_button" @click="change_submit">确认修改</div>
			
		</div>
	</div>
</template>

<script>
import { Toast } from 'mint-ui';
	export default {
		name:'Login',
		data(){
			return{
				old_psw:"",
                new_psw:"",
                confirm_psw:"",
				//个人账户号码
				phone:''
                
			}
		},
		methods:{
			back(){
				this.$router.go(-1)
			},
			go_forget(){
				this.$router.push("/forgetpsw")
			},
			change_submit(){
				//输入内容不为空
				if(this.old_psw!=""&&this.new_psw!==""&&this.confirm_psw!==""){
					//匹配6-15位旧密码
					var pswreg = /^[a-zA-Z0-9]{6,15}$/;
					if(!pswreg.test(this.old_psw)){
						let instance = Toast('请输入正确的旧密码');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;        
                    }
                    //匹配正确6-15位新密码
                    if(!pswreg.test(this.new_psw)){
						let instance = Toast('请设置正确的新密码');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
                    }
                    //判断两次密码是否一致
                    if(this.confirm_psw!==this.new_psw){
						let instance = Toast('两次输入的密码不一致');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
                    }
                    //判断旧密码新密码是否一致
                    if(this.old_psw == this.new_psw){
						let instance = Toast('新密码不能与旧密码相同');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
                    }
                    
                    //输入内容正确，进行修改请求

                   const baseUrl = this.$ChangePsw+"?phone="+this.phone+"&password="+this.old_psw+"&newPassword="+this.new_psw;
					this.$http({
						method:'POST',
						url:baseUrl,
						headers: {
   							 "content-type": "application/json",
							 "Accept" : "*/*"
						 }
					
					}).then((response)=>{
						console.log(response)
						let instance = Toast(response.data.resultMessage);
						setTimeout(() => {
						instance.close();
						}, 2000);
						//修改成功后显示状态，2S后跳转账户设置页
						var timers = setTimeout(()=>{  
								if(response.data.resultCode == 0)  
								{    
									this.$router.push("/my/setting")
								}  
							},2000);  
						
					})

				}else{					
						let instance = Toast('请输入完整的信息');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;                        
                  		
				}
			
			}
		},
		updated(){			
			if(this.old_psw!=""&&this.new_psw!==""&&this.confirm_psw!==""){
					 document.getElementsByClassName("login_button")[0].style.background="#f94b4b";
		  }else{
			  document.getElementsByClassName("login_button")[0].style.background="#c1c1c1";
		  }  
	  
		},
		beforeMount(){

			this.phone = JSON.parse(localStorage.getItem("userId")).phone

		}
	}
</script>

<style lang="scss" scoped>
  .login{
  	width: 100%;
  	height: 100%;
  	background: #f8f8f8;
	
  }	
  .login .login_header{
  	width: 100%;
	height: 2.85rem;
  	background: #f9e44b;
  	padding-left: 0.62rem;
  	padding-right: 0.62rem;
  	box-sizing: border-box;
  	display: flex;
  	justify-content: space-between;
  }
  .login .login_header .back{
  	width: 2.62rem;
  	height: 100%;
  	background: url(../../assets/back.png) no-repeat left center;
  	background-size: 0.62rem 1.28rem;
  }
  .login .login_header .center{
  	width: 4.61rem;
  	height: 2.81rem;
  	text-align: center;
  	line-height: 2.81rem;
  	font-size: 1.12rem;
    color: #72463d;
  }
  .block{
  	width: 2.62rem;
  }
  
  .login_form{
  	box-sizing: border-box;
  	width:100%;
  	height: 11.34rem;
  	padding: 0 0.62rem;
  	padding-top: 1.25rem;
  }
  .login_form .phoneNumber,.login_form .password{
  	width: 100%;
  	height: 2.59rem;
  	padding-left: 0.75rem;
  	box-sizing: border-box;
  }
  .login_form .phoneNumber{
  	background: url(../../assets/locak.png) no-repeat left center;
  	background-size: 0.68rem 1.12rem;
  }
  .login_form .password{
  	background: url(../../assets/sure.png) no-repeat left center;
  	background-size: 0.75rem 0.87rem;
  }
  .login_form .phoneNumber input,.login_form .password input{
  	display: block;
  	border: none;
  	border-bottom: 0.03rem solid #c1c1c1;
  	width: 100%;
  	height: 2.59rem;
  	background: #f8f8f8;
  	padding-left: 1.25rem;
  	box-sizing: border-box;
  }
  #login_phone::-webkit-input-placeholder,#login_psw::-webkit-input-placeholder{
  	 	color: #c6c6c6;
		font-size: 0.93rem;
		
  }
  .login_button{
  	width: 100%;
  	height: 2.03rem;
  	background: #c7c7c7;
  	text-align: center;
  	line-height: 2.03rem;
  	color: #ffffff;
  	font-size: 0.87rem;
  	margin-top: 3.125rem;
  	border-radius: 0.31rem;
  }
  .to_register{
  	width: 3.75rem;
  	height: 1.71rem;
  	margin-top: 0.62rem;
	float: left;
  	text-align: center;
  	line-height: 1.71rem;
  	color: #f94b4c;
  	font-size: 0.68rem;
  }

</style>


