<template>
    <div class="buy_class">
        <div class="header">
            <div class="back" @click="buyBack"></div>
			<div class="center">优惠商城</div>
            <div class="block"></div>
        </div>

        <div class="bottom">

            <div class="box" v-for="data in datalist" :key="data.id" @click="goDetail(data)">
                <div class="content_shop">
                    <div class="style"></div>
                    <div class="name">商品名商品名商品名商品名商品名商品名</div>
                    <div class="price">到手价￥4999.21</div>
                    <div class="old_price">原价￥5000</div>
                    <div class="btm">
                        <div class="logo"></div>
                        <div class="merchant">商家名称</div>
                        <div class="num">2000人购买</div>
                    </div>
                </div>
            </div>

        </div>
        
    </div>
</template>

<script>
    export default {
        name:"FreeClass",
        data(){
            return{
                datalist:[{id:1},{id:2},{id:3},{id:4},{id:5},{id:6},{id:7},{id:8},{id:9}]
            }
        },
        methods: {
            buyBack(){
                this.$router.push('/discount')
            },
            //点击转第三方
            goDetail(data){
                console.log(data.id)
                this.$router.push({
                    path:'/third',
                    query:{
                        title:"优惠商城",
                        url:"http://touch.podinns.com/mobile/buycard/20180417"
                    }
                })
            }
        }
    }
</script>

<style lang="scss">
    .buy_class{
         	width: 100%;
            height: 100%;
            background: #f8f8f8;      
            display: flex;
            flex-direction: column;
       
        .header{
            width: 100%;
	height: 2.85rem;
            background: #fae44c;
            padding-left: 0.62rem;
            padding-right: 0.62rem;
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            .back{
            width: 2.62rem;
            height: 100%;
            background: url(../../assets/back.png) no-repeat center center;
            background-size: 0.62rem 1.28rem;
                }
                .center{
            width: 7.61rem;
            height: 2.81rem;
            text-align: center;
            line-height: 2.81rem;
            font-size: 1.12rem;
            color: #72463d;
            }
            .block{
                width: 2.62rem;
            }
 	     }

        .bottom{
            height: 100%;
            width: 100%;
            overflow: auto;
            background: #ffffff;
            padding:0 .625rem;
            padding-bottom:.625rem;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            .box{
                height: 16.25rem;width: 9.4375rem;
                background: #ffffff;
                .content_shop{
                    width: 100%;height: 100%;padding-top:1rem;border-bottom:.03125rem solid #bfbfbf;
                    .style{height: 9.40625rem;width: 9.40625rem;border: .03125rem solid #8c8c8c;}
                    .name{width: 100%;height: 1.5625rem;line-height: 1.5625rem;overflow: hidden;
                            text-overflow: ellipsis;white-space: nowrap;color: #010101;font-size: .84375rem;}
                    .price{height: 1.125rem;line-height: 1.125rem;color: #ff0000;}
                    .old_price{height: 1.71875rem;line-height: 1.71875rem;color: #9f9f9f;font-size: .6875rem;text-decoration: line-through;}
                    .btm{
                        width: 100%;height: 1.0625rem;line-height: 1.0625rem;font-size: .625rem;
                        .logo{height: 1.03125rem;width: 1.03125rem;border:.03125rem solid #000;float: left;}
                        .merchant{float: left;margin-left: .3125rem;}
                        .num{float: right;}
                        }
                }
            }
           
        }

    }
</style>


