<template>
	<div class="bigbox">
		<swiper :options="swiperOption" ref="mySwiper" >
			    <!-- slides -->
			    <swiper-slide>
			    	<div class="firstYD">
			    		<img src="../assets/yindao1.png" class="img1"/>
			    		<p class="p1">无论何时都有现金可赚</p>
			    	
			    	</div>
			    	
			    </swiper-slide>
			    <swiper-slide>
			    	<div class="secondYD">
			    		<img src="../assets/yindao2.png" class="img2"/>
			    		<p class="p1">无论何地都有优惠可领</p>
			    	
			    	</div>
			    </swiper-slide>
			    <swiper-slide class="thirdYD">
			    	 <img src="../assets/yindao3.png" class="img3"/>
			    	 <p class="p1">无论怎样，都会有超多惊喜！</p>
			    
			    	<div class="start_now" @click="start">立即体验</div>
			    </swiper-slide>
			     <div class="swiper-pagination"  slot="pagination"></div>
     
  </swiper>
	</div>
</template>

<script>
	import 'swiper/dist/css/swiper.css'
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	
	//引导页的暴露
	export default{
		name:'Yindao',
		data(){
			return{
				   swiperOption: {
				   		notNextTick:true,
	          			autoplay: false,
	          			pagination: {
    					 el: '.swiper-pagination'
   					 }
		        }
			}
		},
		components:{swiper,swiperSlide},
		methods:{
			start(){
				//向父推送点击事件
				this.$emit("to-parent","类耗啊")
				//第一次点击存状态
				window.localStorage.setItem("yindao","cun")
			}
		}
	}
</script>

<style scoped>
	
		.bigbox{
			width: 100%;
			height: 100%;
			
		}
		.swiper-container{
			width: 100%;
			height: 100%;
		}
		.firstYD,.secondYD,.thirdYD{
			width: 100%;
			height: 100%;
			background: #fcde4c;
			overflow: hidden;
		}
		.img1{
			display: block;
			width: 100%;
			height:18.68rem;
			margin-top: 7.81rem;
		}
		.img2{
			display: block;
			margin-top: 6.71rem;
			width: 100%;
			height: 19.65rem;
		}
		.img3{
			display: block;
			width: 100%;
			height: 19.78rem;
			margin-top: 6.4rem;
		}
		.p1{
			width: 100%;
			height: 1.41rem;
			text-align: center;
			line-height: 1.41rem;
			font-size: 1.2rem;
			font-weight: 500;
			color: #313131;
			margin-top: 5.4rem;
		}
		.p2{
			width: 100%;
			height: 1.52rem;
			text-align: center;
			line-height: 2.55rem;
			color: #2f312e;
			font-size: 0.87rem;
		}
		.start_now{
			width: 7.9rem;
			height: 2.65rem;
			border: 1px solid #f7254c;
			margin-top: 1.34rem;
			margin-left: 7.84rem;
			text-align: center;
			line-height: 2.65rem;
			font-size: 1.31rem;
			color: #f7254c;
			border-radius: 0.80rem;
		}
</style>


