<template>
	<div class="login">
		<div class="login_header">
			<div class="back" @click="back"></div>
			<div class="center">手机登录</div>
			<div class="block"></div>
		</div>
		<div class="login_form">
			<div class="phoneNumber">
				<input type="text" name="" id="login_phone" value="" placeholder="输入手机号" v-model="login_number"/>
			</div>
			<div class="password">
				<input type="password" name="" id="login_psw" value="" placeholder="输入登录密码" v-model="login_psw"/>
			</div>
			<div class="login_button" @click="login_submit">登录</div>
			<div class="register_button" @click="register">注册</div>
			<div class="xieyi">
				<input type="checkbox" v-model="checkOrNot" class="checkOrNot"/>  已阅读并同意<span class="to_xieyi" @click="userAgreement"> 《米多星用户协议服务》 </span>
				
			</div>
			<div class="to_register" @click="go_forget">忘记密码？</div>
		</div>
	</div>
</template>

<script>
import { Toast } from 'mint-ui';
	export default {
		name:'Login',
		data(){
			return{
				login_number:"",
				login_psw:"",
				//协议
				checkOrNot:true
			}
		},
		methods:{
			back(){
				this.$router.go(-1)
			},
			register(){
				this.$router.push('/register')
			},
			go_forget(){
				this.$router.push("/forgetpsw")
			},
			//用户协议
			userAgreement(){
				this.$router.push('/userAgreement')
			},
			login_submit(){
				//输入内容不为空
				if(this.login_number!=""&&this.login_psw!==""){
					//是否阅读协议协议
					 if(this.checkOrNot == false){
						let instance = Toast('请阅读米多星协议服务');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
					 }
					
						//判断是否输入正确手机号
					var myreg=/^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;  
					if (!myreg.test(this.login_number)) {
						this.login_number = this.login_number.slice(0,11)
						let instance = Toast('请输入正确的手机号');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
					}
					//匹配6-15位密码
					var pswreg = /^[a-zA-Z0-9]{6,15}$/;
					if(!pswreg.test(this.login_psw)){
						let instance = Toast('请输入正确的密码');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
					}
					
					//输入内容正确，进行登录请求
		
					this.$http({
						method:'POST',
						url:this.$login,
						headers: {
   							 "content-type": "application/json"
						 },
						responseType:'text/plain',
						 data:{
						 	password: this.login_psw,
						 	 phone: this.login_number
						 }
					}).then((response)=>{
						//console.log(response.data)
						let instance = Toast(response.data.resultMessage);
						setTimeout(() => {
						instance.close();
						}, 2000);
						
						//登录成功后显示状态，1S后跳转首页
						var timers = setInterval(()=>{  
								if(response.data.resultCode == 0)  
								{  
									console.log(response.data.data)
									clearInterval(timers);  
									this.$router.push("/makemoney/jisu")
									let info = JSON.stringify(response.data.data)
									localStorage.setItem("userId",info)
								}  
							},1000);  
						
					})

				}else{					
						let instance = Toast("请输入完整的信息");
						setTimeout(() => {
						instance.close();
						}, 2000);					
				}
			
			}
		},
		updated(){			
			if(this.login_number!=""&&this.login_psw!==""){
					 document.getElementsByClassName("login_button")[0].style.background="#eb7171";
					 document.getElementsByClassName("login_button")[0].style.color="#fcecec";
		  }else{
			  document.getElementsByClassName("login_button")[0].style.background="#c1c1c1";
			  document.getElementsByClassName("login_button")[0].style.color="#545454";
		  }  
	  
		}
	}
</script>

<style lang="scss" scoped>
  .login{
  	width: 100%;
  	height: 100%;
  	background: #f8f8f8;
	
  }	
  .login .login_header{
  	width: 100%;
	height: 2.85rem;
  	background: #f9e44b;
  	padding-left: 0.62rem;
  	padding-right: 0.62rem;
  	box-sizing: border-box;
  	display: flex;
  	justify-content: space-between;
  }
  .login .login_header .back{
  	width: 2.62rem;
  	height: 100%;
  	background: url(../assets/back.png) no-repeat left center;
  	background-size: 0.62rem 1.28rem;
  }
  .login .login_header .center{
  	width: 4.61rem;
  	height: 2.81rem;
  	text-align: center;
  	line-height: 2.81rem;
  	font-size: 1.12rem;
    color: #72463d;
  }
  .block{
  	width: 2.62rem;
  }
  
  .login_form{
  	box-sizing: border-box;
  	width:100%;
  	height: 17.34rem;
  	padding: 0 0.62rem;
  	padding-top: 1.25rem;
  }
  .login_form .phoneNumber,.login_form .password{
  	width: 100%;
  	height: 2.59rem;
  	padding-left: 0.75rem;
  	box-sizing: border-box;
  }
  .login_form .phoneNumber{
  	background: url(../assets/phone.png) no-repeat left center;
  	background-size: 0.68rem 1.12rem;
  }
  .login_form .password{
  	background: url(../assets/locak.png) no-repeat left center;
  	background-size: 0.75rem 0.87rem;
  }
  .login_form .phoneNumber input,.login_form .password input{
  	display: block;
  	border: none;
  	border-bottom: 0.03rem solid #c1c1c1;
  	width: 100%;
  	height: 2.59rem;
  	background: #f8f8f8;
  	padding-left: 1.25rem;
  	box-sizing: border-box;
  }
  #login_phone::-webkit-input-placeholder,#login_psw::-webkit-input-placeholder{
  	 	color: #c6c6c6;
		font-size: 0.93rem;
		
  }
  .login_button{
  	width: 100%;
  	height: 2.03rem;
  	background: #c1c1c1;
  	text-align: center;
  	line-height: 2.03rem;
  	color: #545454;
  	font-size: 0.87rem;
  	margin-top: 1.25rem;
  	border-radius: 0.31rem;
  }
  .register_button{
  		width: 100%;
  	height: 2.03rem;
  	text-align: center;
  	line-height: 2.03rem;
  	color: #545454;
  	border: 1px solid #ee9393;
  	font-size: 0.87rem;
  	margin-top: 1.25rem;
  	border-radius: 0.31rem;
  }
  .to_register{
  	width: 4.75rem;
  	height: 1.71rem;
  	margin-top: 0.62rem;
	float: right;
  	text-align: center;
  	line-height: 1.71rem;
  	color: #f94b4c;
  	font-size: 0.68rem;
  }
  .xieyi{
  	width: 14.5rem;
  	height: 2.59rem;
  	line-height: 3.05rem;
  	float: left;
  	font-size: 0.75rem;
  	color: #2a2a2a;
  	display: flex;
  }
  .xieyi .checkOrNot{
  	 margin-top: 1.13rem;
  }
  .xieyi .to_xieyi{
  	color: #f94b4b;
  }

</style>


