<template>
    <div class="taskRecord_main">
        <div class="task_header">
			<div class="top">
                <div class="back" @click="taskRecord_back"></div>
			    <div class="center">任务记录</div>
            </div>
        <div class="coupon_tab">
            <router-link tag="div" :to="'/my/setting/TaskRecordMain/taskjisu'">极速任务</router-link>
            <router-link tag="div" :to="'/my/setting/TaskRecordMain/taskbanka'">办卡任务</router-link>	
        </div>
		</div>
        <div class="content">
            <router-view></router-view>
        </div>
        
    </div>
</template>

<script>
    export default {
        name:"taskRecordMain",
        data(){
            return{

            }
        },
        methods: {
            taskRecord_back(){
                this.$router.push("/my");
            }
        }

    }
</script>

<style lang="scss">
  .taskRecord_main{
  	width: 100%;
  	height: 100%;
  	background: #f8f8f8;
    display: flex;
    flex-direction: column;
    .task_header{
        width: 100%;
        height: 5.4375rem;
        background: #fae44c;
         overflow: hidden;
       .top{
           height: 1.34375rem;
           width: 100%;
           text-align: center;
           margin-top: 0.96875rem;
           margin-bottom: 1.21875rem;
            .back{
                float: left;
                margin-left: .625rem;
                width: 0.62rem;
                height: 100%;
                background: url(../../../assets/back.png) no-repeat center center;
                background-size: 0.62rem 1.28rem;
                justify-content: space-around;
             }
       }
    }
    .coupon_tab{
        height: 1.25rem;
        width: 100%;
        display: flex;
        justify-content: space-around;
        .router-link-active{
            color: #f94b4b;
        }
        div{
            height: 100%;
            line-height: 1.25rem;
            width: 3.75rem;
            border: .03125rem solid #434343;
            color: #545454;
            font-size: .78125rem;
            text-align: center;
            border-radius: .3125rem;
            
        }
    }
    .content{
        flex:1;
        overflow: hidden;
        overflow-y: scroll;
    }
  }	
   
</style>



