<template>
	<div class="setting">
		<div class="setting_header">
			<div class="back" @click="setting_back"></div>
			<div class="center">账户设置</div>
			<div class="block"></div>
		</div>
		<div class="change_psw" @click="change_psw">修改密码 <span> ＞ </span></div>
		<div class="bind_other"> 
			<div class="weichat" @click="bind_weichat">微信绑定<span>＞</span></div>
			<div class="alipay" @click="bind_alipay">支付宝绑定<span>＞</span></div>
			<div class="phone" @click="bind_newphone">手机号<span>＞</span> <span class="phoneNumber">{{hide_phone_number}}</span></div>
		</div>
		<div class="logout" @click="logOut">退出登录</div>


		<!-- 退出登录弹窗 -->
		<div class="logOut_box" v-if="isOutShow">
			<div class="kuang">
				<div class="logOut_top">
					<div>退出登录</div>
					<div></div><p></p>
					<span>您确定要退出登录吗？</span>
				</div>
				<div class="logOut_btm">
					<div @click="outyes">确认</div>
					<div @click="outno">取消</div>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	export default{
		data(){
			return{
				//要通过读取localstorage来获取手机号
				phoneNumber:"",
				//退出登录弹窗
				isOutShow:false,
				// 隐藏的手机号
				hide_phone_number:""
			}
		},
		methods:{
			setting_back(){
				this.$router.push('/my')
			},
			change_psw(){
				this.$router.push('/my/changePsw')
			},
			bind_weichat(){
				this.$router.push('/my/setting/bindWx')
			},
			bind_alipay(){
				this.$router.push('/my/setting/bindAlipay')
			},
			bind_newphone(){
				this.$router.push('/my/setting/verifyNumber')
			},
			//退出登录
			logOut(){
				this.isOutShow = true;
			},
			//退出登录确定
			outyes(){
				this.isOutShow = false;
				localStorage.removeItem("userId")
				this.$router.push("/my")
			},
			//退出登录取消
			outno(){
				this.isOutShow = false;
			}
		},
		beforeMount () {
			this.phoneNumber = localStorage.getItem("userId");
			this.phoneNumber = JSON.parse(this.phoneNumber).phone;
			this.hide_phone_number = this.phoneNumber.substr(0,3)+"****"+this.phoneNumber.substr(7);
		}
	}
</script>

<style lang="scss" scoped>
		.setting{
			width: 100%;
			height: 100%;
			overflow:hidden;
			background: #e3e3e3;
			display: flex;
			flex-direction: column;
			.logOut_box{
				height: 100%;width: 100%;background: rgba(0,0,0,.25);
				position: fixed;
				.kuang{
					height: 14.0625rem;width: 14.0625rem;background: #fdeeea;
					margin-left: 4.71875rem;margin-top: 13.1875rem;
					border-radius: .45rem;
					.logOut_top{
						height: 8.75rem;background: #e94f29;width: 100%;text-align: center;
						div:nth-child(1){font-size: 1.09375rem;color: #ffffff;line-height: 2.34375rem;}
						div:nth-child(2){width: 4.75rem;height: 3.53125rem;display: inline-block;
										background: url("../../assets/ku.png") no-repeat center center;
										background-size: 4.75rem 3.53125rem;}
						span{font-size: 1.125rem;color: #ffffff;line-height: 2.03125rem;}
					}
					.logOut_btm{height: 2.21875rem;width: 100%;margin-top: 2.15625rem;
								padding: 0 .75rem;display: flex;justify-content: space-between;
								div{height: 2.21875rem;width: 5.65625rem;background: #e94f29;
									border-radius: 1.09375rem;text-align: center;line-height: 2.21875rem;
									font-size: .9375rem;color: #ffffff;
									}
									div:nth-child(2){background: #cfc8c6;}
								}
				}
			}
			.setting_header{
			  	width: 100%;
			  	height: 3.4rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 		  .center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 }
 	 .change_psw{
 	 		padding: 0 0.62rem;
 	 		width: 100%;
 	 		height: 2.15rem;
 	 		background:#ffffff url(../../assets/changePswLock.png) no-repeat 1.10rem center;
 	 		padding-left: 3.72rem;
 	 		line-height: 2.15rem;
 	 		color: #898989;
 	 		font-size: 0.75rem;
 	 		background-size: 1.06rem 1.25rem;
 	 		border-bottom: 0.03rem solid #e3e3e3;
 	 		span{
 	 			float: right;
 	 		}
 	 	
 	 }
 	 .bind_other{
 	 	width: 100%;
 	 	flex: 1;
 	 	background: #ffffff;
 	 	margin-top: 0.62rem;
 	 	padding: 0.71rem 0.62rem;
 	 	.weichat{
 	 		width: 100%;
 	 		height: 2.4rem;
 	 		background: url(../../assets/weixin.png) no-repeat 0.53rem center;
 	 		padding-left: 3.12rem;
 	 		line-height: 2.4rem;
 	 		color: #898989;
 	 		font-size: 0.71rem;
 	 		background-size: 1.34rem 1.09rem;
 	 		border-bottom: 0.03rem solid #e3e3e3;
 	 		span{
 	 			float: right;
 	 		}
 	 	}
 	 	.alipay{
 	 		width: 100%;
 	 		height: 2.68rem;
 	 		margin-top: 0.78rem;
 	 		background: url(../../assets/zhifubao.png) no-repeat 0.53rem center;
 	 		padding-left: 3.12rem;
 	 		line-height: 2.68rem;
 	 		color: #898989;
 	 		font-size: 0.71rem;
 	 		background-size: 1.12rem 1.12rem;
 	 		border-bottom: 0.03rem solid #e3e3e3;
 	 		span{
 	 			float: right;
 	 		}
 	 	}
 	 	.phone{
 	 		width: 100%;
 	 		height:3rem;
 	 		margin-top: 0.46rem;
 	 		background: url(../../assets/iphone.png) no-repeat 0.53rem center;
 	 		padding-left: 3.12rem;
 	 		line-height: 3rem;
 	 		color: #898989;
 	 		font-size: 0.71rem;
 	 		background-size: 1.15rem 1.65rem;
 	 		border-bottom: 0.03rem solid #e3e3e3;
 	 		span{
 	 			float: right;
 	 		}
 	 		.phoneNumber{
 	 			margin-right: 0.93rem;
 	 		}
 	 	}
 	 }
 	 .logout{
 	 	width: 100%;
 	 	height: 3.12rem;
 	 	background:#f94b4b;
 	 	text-align: center;
 	 	line-height: 3.12rem;
 	 	color: #ffffff;
 	 	font-size: 1rem;
 	 }
		}
</style>


