<template>
    <div class="banka_1">
        <div class="banka_2">   
            <div class="nothing" v-if="isNoTask">暂无参与办卡任务</div>
            <div class="process" v-if="!isNoTask">
               <div class="top">提示：核卡通过后，无需激活，奖励一周内到账</div>
        <v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
                <div class="bk_content" v-for="card in cardList" :key="card.id">
                    <div class="bk_top">
                        <div class="style"><img :src="'http://'+card.taskLogo" alt=""></div>
                        <div class="kpinfo">
                            <div class="name">{{card.taskName}} <span>当前状态: {{card.statusDepict}}</span></div>
                            <p>{{card.cardStyle}}</p>
                            <p>姓名：</p>
                            <p>手机:</p>
                            <p>时间：</p>
                            <span class="award" v-if="card.status==7">+{{card.reward}}元</span>
                        </div>
                    </div>
                    <div class="bk_btm">
                        <div class="btn" v-if="card.status==2" @click="check_pr(card)">进度查询</div>
                        <div class="btn" v-if="card.status==4" @click="receive_award(card)">领取奖励</div>
                        <div class="btn" v-if="card.status==3" @click="check_receive(card)">查看原因</div>
                        <div class="btn" v-if="card.status==6" @click="check_get(card)">查看原因</div>
                    </div>
                </div> 
                <div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../../assets/cry.png"/></div>
        </v-loadmore>         
            </div>
        </div>

        <div class="prompt_box" v-if="isShadow"></div>
            <div class="box_list" v-if="isBoxShow">
                <div class="list_top">
                    <p>不通过原因</p>
                    <p>由于{{this.reson}}导致申请不通过</p>
                    <p>注意： 多次申请不通过会影响申请人的征信，请珍重考虑后再申请！</p>
                </div>
                <div class="list_btm">
                    <div @click="list_close">关闭</div>
                    <div @click="re_apply">重新申请</div>
                </div>
             </div>
            

            <div class="box_list" v-if="isBoxsShow">
                <div class="list_top">
                    <p>不通过原因</p>
                    <p>由于{{this.reson}}导致申请不通过</p>
                    <p>注意： 多次申请不通过会影响申请人的征信，请珍重考虑后再申请！</p>
                </div>
                <div class="list_btm">
                    <div @click="redo_task">重做任务</div>
                    <div @click="re_upload">重新上传</div>
                </div>
             </div>
        

    </div>
</template>

<script>
//办卡进度部分
	import { Loadmore } from 'mint-ui';
    export default {
    	components:{
    	  'v-loadmore':Loadmore,
  		  },
        name:"MyGetcardMission",
        data(){
            return{
                id:'',
                //幕布
                isShadow:'',
                //弹窗1是否出现
                isBoxShow:'',
                //弹窗2是否出现
                isBoxsShow:'',
                reson:'xxxxxxxxxx',
                isNoTask:"",
                cardList:[
                        // {id:"1",time:"2018-3-13 18:38:11",status:"银行审核中",yangshi:"",cardName:"卡片名称",cardStyle:"卡片类型",name:"郭靖",phone:"1354000000",btn:"进度查询"},
                        // {id:"2",time:"2018-3-13 18:38:11",status:"平台审核中",yangshi:"",cardName:"卡片名称",cardStyle:"卡片类型",name:"黄蓉",phone:"1354000000"},
                        // {id:"3",time:"2018-3-13 18:38:11",status:"申请成功",yangshi:"",cardName:"卡片名称",cardStyle:"卡片类型",name:"天线宝宝",phone:"1354000000",btn:"领取奖励"},
                        // {id:"4",time:"2018-3-13 18:38:11",status:"申请失败",yangshi:"",cardName:"卡片名称",cardStyle:"卡片类型",name:"天线宝宝",phone:"1354000000",btn:"查看原因"},
                        // {id:"5",time:"2018-3-13 18:38:11",status:"领取失败",yangshi:"",cardName:"卡片名称",cardStyle:"卡片类型",name:"天线宝宝",phone:"1354000000",btn:"查看原因"},
                        // {id:"6",time:"2018-3-13 18:38:11",status:"任务成功",yangshi:"",cardName:"卡片名称",cardStyle:"卡片类型",name:"天线宝宝",phone:"1354000000"}
                        ],
                pageNo:1,
                pageSize:4,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false,
                user:''
            }
        },
        methods:{
            check_pr(card){
                console.log("进度查询")
            },
            receive_award(card){
                console.log("领取奖励")
            },
            check_receive(card){
                console.log("查看申请失败原因")
                this.isBoxShow = true;
                this.isShadow = true;
                this.id = card.id;
            },
            check_get(card){
                console.log("查看领取失败原因")
                this.isBoxsShow = true;
                this.isShadow = true;
                this.id = card.id;
            },
            //弹窗按钮，关闭
            list_close(){
                this.isBoxShow = false;
                this.isShadow = false;
            },
            //弹窗按钮 重新申请
            re_apply(){
                this.isBoxShow = false;
                this.isShadow = false;
                console.log(this.id)
                this.$router.push('/makemoney/banka/getcardmission/reservedInformation'+"/"+this.id)
            },
            //重做任务 跳第三方
            redo_task(){
                this.isShadow = false;
                this.isBoxsShow = false;
            },
            //重新上传  跳领取奖励页面
            re_upload(){
                this.isShadow = false;
                this.isBoxsShow = false;
            },
            loadBottom:function() {
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
		    	    this.$http({
						method:'post',
						url:this.$taskRecordSearch,
						 params:{
                             userId : this.user.id,
                             taskType : 3,
                             "page.pagesize":this.pageSize,
							 "page.currentPage":this.pageNo
						 },
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((response)=>{
                        console.log(response.data.data)
                        if(response.data.data.length == 0){
                            this.allLoaded = true; 
							this.isAll = show;
                            // console.log("null")
                            if(this.cardList.length == 0){
                                this.isNoTask = true
                            }else{
                                this.isNoTask = false
                            }

                        }else{
                            this.cardList = this.cardList.concat(response.data.data);
                        }
					}).catch((err)=>{
						console.log(err)
                    })	
		    },
		   more(){
		   	this.pageNo++
		   	this.loadList(true);
		   }
        },
        beforeMount(){
           
        },
        mounted(){
            let user = localStorage.getItem("userId")
            user = JSON.parse(user)
            this.user = user
            this.loadList(false);

          
        }
    }
</script>

<style lang="scss">
    .banka_1{
        height: 80%;
        width: 100%;
        display: flex;
        overflow: hidden;
        .prompt_box{
            height: 100%;width:100%;position: absolute;background: rgba(0, 0, 0, 0.45);top: 0;}
            .box_list{
                height: 15.40625rem;width: 14.75rem;
                position: absolute;    
                top: 35%;left: 19%;
                background: url("../../../../assets/tanchuang.png") no-repeat center center;
                background-size: 14.75rem 15.40625rem;
                border-radius: .5625rem;
                .list_top{height: 7.5625rem;width: 100%;padding: 0 1rem;
                        p:nth-child(1){height: 1.375rem;line-height: 1.375rem;font-size: .6875rem;color: #ffffff;font-size: .9375rem;
                                        margin-bottom: .3rem;text-align: center;margin-top: 5.03125rem;}
                        p:nth-child(2){width: 100%;overflow: hidden;font-size: .6875rem;line-height: 1.125rem;margin-top: 1.3rem;margin-bottom: .7rem;}
                        p:nth-child(3){font-size:.5625rem;line-height: 1rem;color: #ff0000;}
                        }
                .list_btm{display: flex;
                        div{width: 50%;text-align: center;height: 1.84375rem;line-height: 2.84375rem;}
                        div:nth-child(1){}
                        }

            }
       
        .banka_2{
            height: 100%;
            width: 100%;
            flex: 1;
            overflow: auto;
            .nothing{
                height: 45%;
                width: 100%;
                position: absolute;
                top: 55%;
                color: #5c5c5c;
                font-size: .875rem;
                text-align: center;
            }
            .process{
                height: 100%;
                width: 100%;
                .top{
                    height: 1.93rem;
                    line-height: 1.875rem;
                    width: 100%;
                    text-align: center;
                    font-size: .75rem;
                    color: #f94b4b;
                    
                }
                .bk_content{
                    height: 7.375rem;
                    background: #ffffff;
                    width: 100%;
                    border-top: .03125rem solid #e1e1e1;
                    box-shadow: 0rem 0.215rem 0.625rem #888686;
                    border-bottom: .03125rem solid #6a6969;
                    margin-bottom: .46875rem;
                    padding: 0 .625rem;
                    .bk_top{
                        height: 4.53125rem;width: 100%;
                        border-bottom: .03125rem solid #535353;
                        margin-top: .34375rem;
                        display: flex;
                        .style{height: 4.1875rem;width: 7.90625rem;border: .03125rem solid #959595;margin-right: .4375rem;
                                img{height: 100%;width: 100%;}
                                }
                        .kpinfo{width: 100%;height: 100%;position: relative;
                                .award{position: absolute;right: 0;top: 2rem;color: #f94b4b;font-weight: bold;}
                                .name{color: #010101;font-size: .71875rem;
                                    span{float: right;color: #f94b4b;}
                                    }
                                p:nth-child(2){font-size: .5625rem;color: #9a9a9a;line-height: .78125rem;}
                                p:nth-child(3){font-size: .75rem;color: #010101;line-height: .9375rem;}
                                p:nth-child(4){font-size: .75rem;color: #010101;line-height: .9375rem;}
                                p:nth-child(5){font-size: .75rem;color: #9a9a9a;line-height: .9375rem;}
                                }
                    }
                    .bk_btm{
                        height: 1.5625rem;width: 100%;margin-top: .5rem;
                        .btn{height: 1.5625rem;line-height: 1.5625rem;width: 3.4375rem;text-align: center;
                            font-size: .75rem;color: #ffffff;background: #f94b4b;float: right;
                            }
                    }
                }
            }
        }
    }
</style>


