<template>
    <div class="DrawCashSubmitted">
        <div class="back" @click="finish">完成</div>
        <div class="main">
            <div class="content">
                <div class="top"></div>
                <div>{{form.title}}</div>
                <div>预计到账时间： 一个工作日到账，节假日延后</div>
                <div class="form">
                    <div><span>{{form.s11}}</span><span>{{form.s12}}</span></div>
                    <div><span>{{form.s21}}</span><span>{{form.s22}}</span></div>
                    <div><span>{{form.s31}}</span><span>{{form.s32}}</span></div>
                    <div v-if="!isPhone"><span>{{form.s41}}</span><span>{{form.s42}}</span></div>
                </div>
                <div class="btm">
                    <img src="../../../assets/book.png" @click="goRecord">
                    <p @click="goRecord">提现记录</p>
                </div>
                


            </div>
        </div>
       
    </div>
</template>

<script>
    export default {
        name:'DrawCashSubmitted',
        data(){
            return{
                //表的内容
                form:{title:"",s11:"",s12:"",s21:"",s22:"",s31:"",s32:"",s41:"",s42:""},
                //是否是手机充值
                isPhone:""
            }
        },
        methods: {
            finish(){
                this.$router.go(-1)
            },
            goRecord(){
                this.$router.push('/my/drawCash/drawCashRecord')
            }
        },
        beforeMount(){
            const value = this.$route.query;
            if(value.status == "wx"){
                this.form.title = "提现申请已提交";
                this.form.s11 = "真实姓名";this.form.s21 = "微信账号";this.form.s31 = "提现金额";this.form.s41= "账户余额";
                this.form.s12 = value.name; this.form.s22 = value.account;this.form.s32=value.price;this.form.s42=value.left;
                this.isPhone = false;
             }else if(value.status == "zfb"){
                this.form.title = "提现申请已提交";
                this.form.s11 = "支付宝账号";this.form.s21 = "收款人姓名";this.form.s31 = "提现金额";this.form.s41= "账户余额";
                this.form.s12 = value.name; this.form.s22 = value.account;this.form.s32=value.price;this.form.s42=value.left;
                this.isPhone = false;
             }else{
                this.form.title = "充值申请已提交";
                this.form.s11 = "充值号码";this.form.s21 = "提现金额";this.form.s31 = "账户余额";
                this.form.s12 = value.name; this.form.s22 = value.price;this.form.s32=value.left;
                this.isPhone = true;
             }
        }
    }
</script>

<style lang="scss">
    .DrawCashSubmitted{
        width: 100%;height: 100%;overflow: hidden;
        .back{
            width: 2.62rem;
            height: 1.28125rem;
            color: #131313;
            margin-right: .625rem;margin-top: .75rem;
            position: absolute;right: 0;
        }
        .main{height: 100%;width: 100%;text-align: center;
            .content{
                width: 73%;height: 100%;margin-top: 1.40625rem;display: inline-block;
                .btm{
                    img{margin-left: .65625rem;margin-top: .96875rem;display: block;}
                    p{margin-left: .65625rem;margin-top: .90625rem;font-size: .71875rem;color: #f94b4b;float: left;}
                    }
                .top{height: 4.84375rem;width: 100%;background: url(../../../assets/right.png) no-repeat center center;
                    background-size: 6.5rem 4.84375rem;
                    }
                div:nth-child(2){margin-top: 1.46875rem;font-size: 1.5625rem;margin-bottom: 1.125rem;}
                div:nth-child(3){font-size: .8125rem;margin-bottom: .875rem;}
                .form{
                    width: 100%;border-top: .03125rem solid #aaaaaa;
                    color:#727272;border-bottom: .03125rem solid #aaaaaa;padding-bottom:1.25rem;
                    div{
                        margin-top: 1.25rem;font-size: .84375rem;
                        height: .875rem;width: 100%;
                        span:nth-child(1){float: left;margin-left: .84375rem}
                        span:nth-child(2){float: right;margin-right: .84375rem}
                    }
                }
            }
        }
    }
</style>


