<template>
    <div class="change_succed">
        <div class="change_succed_header">
			<div class="back" @click="BindSucceed_back">完成</div>
	   </div>
       <div class="bgphone"></div>
       <p class="p1">恭喜您，您已换绑成功!</p>
       <p class="p2"><span>当前绑定手机号</span><span>{{this.phoneNumber}}</span></p>
    </div>
</template>

<script>
    //读取缓存登录手机号码
	var phone_number = localStorage.getItem("userId");
    export default {
        name:"ChangePhoneSucceed",
        data(){
            return{
                phoneNumber:''
            }
        },
        methods: {
            BindSucceed_back(){
				this.$router.push('/my/setting')
			}
        },
        beforeMount(){
            phone_number = JSON.parse(localStorage.getItem("userId")).phone
			//隐藏手机号中间4位	
			this.phoneNumber = phone_number.substr(0,3)+"****"+phone_number.substr(7); 
			
		}
    }
</script>

<style lang="scss">
    .change_succed{
		width: 100%;
		height: 100%;
		background: #f8f8f8;
		.change_succed_header{		
			  	width: 100%;
	height: 2.85rem;
                line-height: 4.06rem;
			  	padding-left: .625rem;
				  .back{
				  	width: 2.62rem;
				  	height: 100%;
				    float: right;
				  	font-size: 1rem;
				 		 }
		}
        .bgphone{
            height: 12.21875rem;
            width: 100%;
            margin-top: 2.25rem;
            background: url(../../../assets/changeBindOk.png) no-repeat center center;
            background-size: 6.5625rem 7.5rem;
        }
        .p1{
            font-size: 1.03125rem;
            width: 100%;
            text-align: center;
        }
        .p2{
            font-size: 1.03125rem;
            width: 100%;
            margin-top: 1.84375rem;
            span:nth-child(1){
                margin-left: 4.28125rem;
                margin-right: 3.125rem;
            }
        }
        
    }
</style>


