<template>
	<div class="bindNewPhone">
		<div class="bindNewPhone_header">
			<div class="back" @click="bindPhone_back"></div>
			<div class="center">绑定新手机号</div>
			<div class="block"></div>
		</div>
		<div class="newPhone_form">
			<div class="phoneNumber">
				<input type="text" name="" id="bindNewPhone_phone" value="" placeholder="输入手机号" v-model="new_number"/>		
			</div>
			<div class="yanZheng">
				<input type="text" id="bindNewPhone_yanZ"  placeholder="输入手机短信验证码" v-model="validateCode"/>
				<div class="get_code">
					<span v-show="show" @click="getCode">获取验证码</span>
					<span v-show="!show" class="count">约{{count}}s后获取</span>
				</div>			
			</div>			
		</div>
		<div class="bindNewPhone_submit" @click="bindNewPhone_submit"><span class="submit_button">提交</span></div>

	</div>
</template>

<script>
import { Toast } from 'mint-ui';
	export default{
		data(){
			return{
				new_number:"",
				validateCode:""	,
				show:true,
				count:"",
				phone_number:''
			}
		},
		methods:{
			bindPhone_back(){
					this.$router.go(-1)
			},
			getCode(){
				  //判断手机号是否输入正确
					 var myreg=/^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;  
					if (!myreg.test(this.new_number)) {  
						this.new_number = this.new_number.slice(0,11)
						let instance = Toast('请输入正确的手机号');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
					}else{
						//输入验证码正确，进行短信验证
						const baseUrl = this.$ChangeBind_Msg+this.new_number;
						this.$http({
						method:'get',
						url:baseUrl,
						  headers: {
							"Accept" : "*/*"
						  },
					}).then((response)=>{
						console.log(response)
					}).catch((err)=>{
						console.log(err)
					})
						//60s倒计时
						const TIME_COUNT = 60;
						if (!this.timer) {
						this.count = TIME_COUNT;
						this.show = false;
						this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= TIME_COUNT) {
							this.count--;
							} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
							}
						}, 1000)
						}
					}
			},
			bindNewPhone_submit(){
				var myreg=/^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/; 
				if (!myreg.test(this.new_number)) {  
						this.new_number = this.new_number.slice(0,11)
						let instance = Toast('请输入正确的手机号');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
				}
				if(this.validateCode==""||this.new_number==""){
						let instance = Toast('请输入完整信息');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
				}else{
					//请求 判断返回值 是否跳转
					 this.$http({
						method:'post',
						url:this.$ChangeBindNew,
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
						  params:{
							  'phone':this.phone_number,
							  'newPhone':this.new_number,
							  'validateCode':this.validateCode
						  }
					}).then((response)=>{
						console.log(response)
							let instance = Toast(response.data.resultMessage);
							setTimeout(() => {
							instance.close();
							}, 2000);

						if(response.data.resultCode == 0){
						var timers = setTimeout(()=>{  
								// 重新存用户信息的缓存
									let user = localStorage.getItem("userId");
									user = JSON.parse(user)
									 user.phone = this.new_number;
									 user = JSON.stringify(user)
									localStorage.setItem("userId",user)
									clearInterval(timers);  
									this.$router.push("/my/setting/verifyNumber/ChangeBind/ChangePhoneSucceed") 	
								
							},2000);  
						
						}
						}).catch((err)=>{
						console.log(err)
						})




				}
			}
		},
		 updated () {
		if(this.validateCode!==""&&this.new_number!==""){
					 document.getElementsByClassName("submit_button")[0].style.background="#f94b4b";
					 document.getElementsByClassName("submit_button")[0].style.color="#ffffff";
		  }else{
			  document.getElementsByClassName("submit_button")[0].style.background="#c1c1c1";
		  }  
	  },
	  beforeMount(){
		if(localStorage.getItem("userId")){
			var phone_number = localStorage.getItem("userId");
			phone_number = JSON.parse(phone_number).phone;
			this.phone_number = phone_number
			
		}
	  }
	}
</script>

<style lang="scss" scoped>
.bindNewPhone{
	width: 100%;
	height: 100%;
	background:#f8f8f8 ;
	.bindNewPhone_header{
			  	width: 100%;
				height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 		  .center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 
	}
	.newPhone_form{
		width:100%;
		height: 5.18rem;
		margin-top: 1.18rem;
		padding: 0 0.62rem;
		.phoneNumber{
			 	width: 100%;
  				height: 2.59rem;
  				padding-left: 0.75rem;
  				background: url(../../../assets/phone.png) no-repeat left center;
  				background-size: 0.68rem 1.12rem;
  				input{
  						display: block;
				  	border: none;
				  	border-bottom: 0.03rem solid #c1c1c1;
				  	width: 100%;
				  	height: 2.59rem;
				  	background: #f8f8f8;
				  	padding-left: 1.25rem;
  				}
		}
		.yanZheng{
			width: 100%;
		  	height: 2.59rem;
		  	padding-left: 0.75rem;
		  	background: url(../../../assets/yanZ.png) no-repeat left center;
		  	background-size: 0.78rem 0.95rem;
		  	position: relative;
		  	input{
  						display: block;
				  	border: none;
				  	border-bottom: 0.03rem solid #c1c1c1;
				  	width: 100%;
				  	height: 2.59rem;
				  	background: #f8f8f8;
				  	padding-left: 1.25rem;
  				}
  				.get_code{
				  	width: 4.68rem;
				  	height: 1.5rem;
				  	border-radius: 0.25rem;
				  	background: #f9e44b;
				  	font-size: 0.75rem;
				  	color: #000002;
				  	position: absolute;
				  	text-align: center;
				  	line-height: 1.5rem;
				  	top: 0.53rem;
				  	right: 0;
 					 }
		}
	}
	.bindNewPhone_submit{		
			width: 100%;
			height:2.03rem;
			padding: 0 0.62rem;
			margin-top: 1.87rem;
			span{
				display: block;
				text-align: center;
				width: 100%;
				height: 100%;
				line-height: 2.03rem;
				font-size: 0.81rem;
				color: #ffffff;
				background: #e2e2e2;
				margin-top: 1.09rem;
				border-radius: 5px;
			}
		
	}

}
</style>


