<template>
	<div class="form_mission_mian">
	<v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
		<div :class="list.surplus!==0?'form_mission_box':'grey'" v-for="list in missionList" :key="list.id" @click="goDetail(list)" >
			<div class="form_mission_logo"><img :src="'http://'+list.taskLogo" alt=""></div>
			<div class="form_mission_name">{{list.taskName}}</div>
			<div class="form_mission_surplus">剩余{{list.taskNumber}}份</div>
			<div class="form_mission_money">￥{{parseInt(list.reward)}}</div>
		</div>
		<div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../../assets/cry.png"/></div>
	</v-loadmore>	
	</div>
</template>

<script>
// 表单注册任务任务列表模块
	import { Loadmore } from 'mint-ui';
	export default{
		components:{
    	  'v-loadmore':Loadmore,
  		},
		name:"FormMessionList",
		data(){
			return{
				missionList:[],
				pageNo:1,
                pageSize:9,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false
			}
		},
		methods:{
			goDetail(list){
				
				if(localStorage.getItem('userId')){			
					if(list.taskNumber == 0){
						return
					}else{
						this.$router.push({path:'/formRegisterTaskDetail'+"/"+list.id})
					}
				}else{
					this.$router.push('/login')
				}
			},
			loadBottom:function() {
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
			loadList(show){
				this.$http({
						method:'POST',
						url:this.$taskSearch,
						headers: {
   							 "content-type": "application/json"
						 },
						responseType:'text/plain',
						 params:{
						 	taskType: 1,
						 	 "page.pagesize":this.pageSize,
						 	 "page.currentPage":this.pageNo
						 }
					}).then((res)=>{
						if(res.data.data.length == 0){
							this.allLoaded = true; 
							this.isAll = show;							
						}else{
							this.missionList = this.missionList.concat(res.data.data)
						}
						
					})
			
			},
			more(){
			   	this.pageNo++
			   	this.loadList(true);
		   }
			
		},
		beforeMount () {
			
		},
		mounted(){		
			this.loadList(false)
		}
		
	}
</script>

<style lang="scss">
.form_mission_mian::-webkit-scrollbar{
			display: none;
		}
	.form_mission_mian{
		width: 100%;
		height: 100%;
		position: relative;
		background: #ffffff;
		padding-top: 0.46rem;
		.grey{
			height: 2.53125rem;
			line-height: 2.53125rem;
			padding-left: .875rem;
			margin-bottom: .9375rem;
			display: flex;
			.form_mission_logo{
				height: 2.53125rem;
				width: 2.53125rem;
			}
			.form_mission_name{
				position: absolute;
				left: 4.96875rem;
				color: #b8b8b8;
				font-size: .6875rem;
			}
			.form_mission_surplus{
				position: absolute;
				left: 12.53125rem;
				color: #b8b8b8;
				font-size: .6875rem;
			}
			.form_mission_money{
				position: absolute;
				right: .53125rem;
				color: #b8b8b8;
			}
		}
		.form_mission_box{
			height: 2.53125rem;
			line-height: 2.53125rem;
			padding-left: .875rem;
			margin-bottom: .9375rem;
			display: flex;
			.form_mission_logo{
				height: 2.53125rem;
				width: 2.53125rem;
				img{height: 100%;width: 100%;}
			}
			.form_mission_name{
				position: absolute;
				left: 4.96875rem;
				color: #2a2a2a;
				font-size: .6875rem;
			}
			.form_mission_surplus{
				position: absolute;
				left: 12.53125rem;
				color: #2a2a2a;
				font-size: .6875rem;
			}
			.form_mission_money{
				position: absolute;
				right: .53125rem;
				color: #f94b4b;
			}
		}
	}
</style>


