<template>
   <div class="redBoxSuccess">
       <div class="getbanner">
           <div class="back" @click="to_index"></div>
       </div>
       <div class="seggest">
           <div v-show="isover" class="tryOther">试试其他任务吧</div>
           <div v-show="!isover" class="seggest_list" v-for="(list,index) in mission_jia" :key="list.id" @click="to_formRegisterTaskDetail(list)">
				<div class="logo">{{list.id}}</div>
                <div class="center">
                    <div class="name">{{list.taskName}}</div>
                    <div class="mount">剩余{{list.taskNumber}}</div>
                </div>
                <div class="reward">{{list.reward}}</div>
			</div>
       </div>
       <div class="shuaxin" @click="shuaxin" v-show="!isover">刷新</div>
       <div class="shuaxin" @click="to_index" v-if="isover">去玩海量任务！</div></div>
   </div>
</template>
 
<script>
    export default{
        data(){
            return{
                    mission_jia:[],
                    currentPage:1,
                    isover:false
            }
        },
        methods:{
            shuaxin(){
                this.currentPage += 1 
                this.$http({
						method:'POST',
						url:this.$taskSearch,
						headers: {
   							 "content-type": "application/json"
						 },
						responseType:'text/plain',
						 params:{
						 	taskType: 1,
						 	 "page.pagesize":7,
						 	 "page.currentPage":this.currentPage
						 }
					}).then((res)=>{					
                        console.log(res.data.data)	
                        this.mission_jia = res.data.data
                        if(this.mission_jia.length == 0){
                            this.isover = true
                        }
					})

            },
            to_index(){
                this.$router.push('/makemoney/jisu')
            },
            to_formRegisterTaskDetail(list){
                    this.$router.push({path:'/formRegisterTaskDetail'+"/"+list.id})
            }
        },
        mounted(){
            this.$http({
						method:'POST',
						url:this.$taskSearch,
						headers: {
   							 "content-type": "application/json"
						 },
						responseType:'text/plain',
						 params:{
						 	taskType: 1,
						 	 "page.pagesize":7,
						 	 "page.currentPage":1
						 }
					}).then((res)=>{					
                        console.log(res.data.data)	
						this.mission_jia = res.data.data
					})
        }
    }
</script>

<style lang="scss" scoped>
    .redBoxSuccess{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        background: #dde6ed;
        .getbanner{
            position: relative;
            width : 100%;
            height: 13.40625rem;
            background: url("../assets/getbanner.jpg") no-repeat center center;
            background-size:100% 100%;
            .back{
                    position: absolute;
                    top: .625rem;
                    left: .625rem;
                    width: 2.62rem;
                    height: 1.28rem;
                    background: url(../assets/back.png) no-repeat center center;
                    background-size: 0.62rem 1.28rem;
                }
        }
        .seggest{
            width: 100%;
            height: 23.96875rem;
            background: #f3f5f9;
            margin-top: .3125rem;
            padding: 0 .9375rem;
            .tryOther{
                width: 100%;
                height: 100%;
                text-align: center;
                line-height:  23.96875rem;
                font-size: 1.5625rem;
                color: #000000;
            }
            .seggest_list{
                    widows: 100%;
                    height: 2.59375rem;
                    display: flex;
                    margin-top:.625rem;
                    .logo{
                        width: 2.59375rem;
                        height: 100%;
                        border: 1px solid #999999;
                        text-align: center;
                        line-height: 2.59rem;
                    }
                    .center{
                        width: 4rem;
                        height: 100%;
                        padding-left: .3125rem;
                        .name{
                            width: 100%;
                            height: 1.84375rem;
                            font-size: .75rem;
                            color: #010101;
                        }
                        .mount{
                            font-size:.625rem;
                            color: #fa4e02;
                        }
                    }
                    .reward{
                        flex: 1;
                        height: 100%;
                        text-align:right;
                        line-height: 2.59375rem;
                    }
            }
        }
        .shuaxin{
            flex: 1;
            background: #fcbf0e;
            text-align: center;
            line-height: 2.71875rem;
            color: #5b6066;
        }
    }
</style>



