<template>
    <div class="banka">
    	<!--筛选-->    	
    	<!--办卡任务请求渲染-->

		<!--<div class="choose_list">
			<div class="btn" @click="list_btn"></div>
			<div class="list">
				<div class="dalei"><p><img src="../../../assets/kapian.png">卡片种类</p></div>
				<div class="xiaolei" :class="task1" @click="mission1"><p>种类不限</p></div>
				<div class="xiaolei" :class="task2" @click="mission2"><p>购物卡</p></div>
				<div class="xiaolei" :class="task3" @click="mission3"><p>车主卡</p></div>
				<div class="xiaolei" :class="task4" @click="mission4"><p>商旅卡</p></div>
				<div class="xiaolei" :class="task5" @click="mission5"><p>女性卡</p></div>
				<div class="xiaolei" :class="task6" @click="mission6"><p>休闲卡</p></div>
				<div class="xiaolei" :class="task7" @click="mission7"><p>金融卡</p></div>
				<div class="xiaolei" :class="task8" @click="mission8"><p>学生卡</p></div>	
				<div class="dalei"><p><img src="../../../assets/jinqian.png">金额排序</p></div>
				<div class="xiaolei" :class="money1" @click="price1"><p>金额不限</p></div>
				<div class="xiaolei" :class="money2" @click="price2"><p>金额↑</p></div>
