			</div>
		</div> -->
        <div class="banka_main">
        	<div class="banka_main_list_content">
				<div class="banka_list" v-for="list in mission_jia" :key="list.id" @click="to_reservedInformation(list)">
					<div class="logo"><img :src="'http://'+list.taskLogo" alt=""></div>
					<div class="name"><span>{{list.taskName}}</span><span>{{list.taskType}}</span></div>
					<div class="mount">
						<span>{{list.taskDescription}}</span>
						<span>{{list.taskDescription}}</span>		
					</div>
					<div class="reward">{{parseInt(list.reward)}}元</div>
				</div>
			</div>
			<div class="banka_more" @click="more">更多&nbsp;<span>></span></div>
		</div>
		 <div class="paihang">
				<div>做任务排行榜</div>
				<div>名次实时更新</div>				
			</div>
			<div class="paihang2">
				<span>奖杯</span>
				<span>头像</span>
				<span>昵称</span>
				<span>任务数</span>
				<span>金额</span>
			</div>
			<div class="paihangContent">
				<div class="cup">
					<div class="one"></div>
					<div class="two"></div>
					<div class="three"></div>
				</div>
				<div class="userTop" v-for="data in datas" :key="data.id">
					<div class="userHeadPic"><img :src="'http://'+data.userHeadPic" /></div>
					<div class="nicName">{{data.userName}}</div>
					<div class="taskNumber">{{data.taskNumber}}</div>
					<div class="amount">{{data.amount}}</div>
				</div>
			</div>
				
    </div>
</template>
           
<script>
	

    export default {
		
        name:"JiSu",
        data(){

        	return{
        		isRight:true,
				//6个类名变量，无需鸟它
				task1:"",
				task2:"",
				task3:"",
				task4:"",
				task5:"",
				task6:"",
				task7:"",
				task8:"",
				money1:"",
				money2:"",
				money3:"",
				mission_jia:[],
				datas:[]
        	}
        },
      
        methods:{
			list_btn(){
				
				if(this.isRight){
					document.getElementsByClassName("choose_list")[0].style.right = "0";
					this.isRight = false;
					this.is_list = true;
					this.$emit("to-parent",false)
				}else{
					document.getElementsByClassName("choose_list")[0].style.right = "-5.4rem";
					this.isRight = true;
					this.$emit("to-parent",true)
				}
			},
			//种类不限按钮事件
			mission1(){
				this.task1="change",this.task2="",this.task3="",this.task4="",this.task5="",this.task6="",this.task7="",this.task8="";
			},
			//购物卡
			mission2(){
				this.task1="",this.task2="change",this.task3="",this.task4="",this.task5="",this.task6="",this.task7="",this.task8="";
			},
			//车主卡
			mission3(){
				this.task1="",this.task2="",this.task3="change",this.task4="",this.task5="",this.task6="",this.task7="",this.task8="";				
			},
			//商旅卡
			mission4(){
				this.task1="",this.task2="",this.task3="",this.task4="change",this.task5="",this.task6="",this.task7="",this.task8="";				
			},
			//女性卡
			mission5(){
				this.task1="",this.task2="",this.task3="",this.task4="",this.task5="change",this.task6="",this.task7="",this.task8="";				
			},
			//休闲卡
			mission6(){
				this.task1="",this.task2="",this.task3="",this.task4="",this.task5="",this.task6="change",this.task7="",this.task8="";
			},
			//金融卡
			mission7(){
				this.task1="",this.task2="",this.task3="",this.task4="",this.task5="",this.task6="",this.task7="change",this.task8="";
			},
			//学生卡
			mission8(){
				this.task1="",this.task2="",this.task3="",this.task4="",this.task5="",this.task6="",this.task7="",this.task8="change";
			},
			//金额不限
			price1(){
				this.money1="change",this.money2="",this.money3=""
			},
			//金额↑
			price2(){
				this.money1="",this.money2="change",this.money3=""
			},
			//金额↓
			price3(){
				this.money1="",this.money2="",this.money3="change"
			},
			more(){
				if(localStorage.getItem("userId")){
					this.$router.push("/makemoney/banka/getcardmission")
				}else{
					this.$router.push('/login')
				}
				

			},
			to_reservedInformation(list){
				if(localStorage.getItem("userId")){
					this.$router.push('/makemoney/banka/getcardmission/reservedInformation'+"/"+list.id)
				}else{
					this.$router.push('/login')
				}
				
			}

	   },
	   mounted(){
	   	//请求任务
		   this.$http({
						method:'post',
						url:this.$taskSearch,
						 params:{
							 taskType:3,
							 "page.pagesize":10,
							 "page.currentPage":"1"
						 },
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((response)=>{
						
						this.mission_jia = response.data.data; 
					}).catch((err)=>{
						console.log(err)
					})	
					
			//请求排行榜
			this.$http({
					method:'POST',
					url:this.$getTop,
			}).then((res)=>{
				this.datas = res.data.data
				console.log(this.datas)
			})
	   },
	   beforeMount(){
		 		
	  	 }
       
    }
</script>

<style lang="scss" scoped>
