				}
				span:nth-child(3){
					margin-left: 1.7375rem;
				}
				span:nth-child(4){
					margin-left: 4.625rem;
				}
				span:nth-child(5){
					margin-left: 4.8rem;
				}
			}
   }
   .choose_list{
	   height: 14.78rem;
	   position: absolute;
	   right: -5.5rem;
	   display: flex;
	   opacity: .6;
	   transition: all 1s;
	   z-index: 100;
	   .btn{
		   background: #fe5306 url("../../../assets/diantong.png") no-repeat center center;
		   background-size:.59375rem .9375rem;
		   width: 1.1875rem;
		   height: 1.625rem;
		   border-top-left-radius: 50%;
