                <div class="right">
                    <div class="name">哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈
                        哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈
                    </div>
                    <div class="feature">到手价格￥ <span>28.21</span></div>
                    <div class="merchant">商家名称</div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name:'',
        data(){
            return{
                datalist:[
                    {id:1,name:''},
                    {id:2,name:''},
                    {id:3,name:''},
                    {id:4,name:''},
                    {id:5,name:''},
                    {id:6,name:''},
                    {id:7,name:''},
                    {id:8,name:''}
                ]
            }
        },
        methods:{

        }
    }
</script>

<style lang="scss">
    .discount_shop{
        height: 100%;
        width: 100%;
        padding: .8125rem .625rem;
        .list{
            height: 5.71875rem;
            width: 100%;
            border-bottom: .03125rem solid #aaaaaa;
            margin-bottom: .875rem;
            .time{
                color: #000000;font-size: .625rem;
                margin-bottom: .59375rem;
            }
            .l_content{
                height: 3.78125rem;width: 100%;display: flex;
                .left{
                    height: 3.6875rem;
                    width: 3.6875rem;
                    border:.0625rem solid #959595;
                    margin-right: .15625rem;
                }
                .right{
                    height: 100%;
                    width: 100%;
                    .name{
                        height: 1.875rem;
                        width: 100%;
                        line-height: .9375rem;
                        font-size: .625rem;
                        color:#8d8d8d;
                         overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                    }
                    .feature{
                        font-size: .8125rem;
                        color:#000000;
                        span{
                            color: #fd2424;
                        }
                    }
                    .merchant{
                        font-size: .71875rem;
                        color: #8d8d8d;
                        margin-top: .1875rem;
                    }
                }
            }
        }
    }
</style>


