<template>
    <div class="box_1">
        <div class="box_2">

         <v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
           <div class="content" v-for="data in datalist" :key="data.id" @click="getDetail(data)">
               <div class="card"><img :src="'http://'+data.taskLogo" alt=""></div>
               <div class="mid">
                   <div class="name"><span>{{data.taskName}}</span><p>{{data.taskType}}</p></div>
                   <div class="feature">{{data.taskDescription}}</div>
                   <div class="feature">{{data.taskDescription}}</div>
               </div>
               <div class="price">{{parseInt(data.reward)}}元</div>
           </div>
           <div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../../assets/cry.png"/></div>
         </v-loadmore> 

        </div>
    </div>
</template>

<script>
//任务列表部分
    import { Loadmore } from 'mint-ui';
    export default {
        components:{
    	  'v-loadmore':Loadmore,
  		  },
        name:"GetcardMissionList",
        data(){
            return{
                datalist:[
                    // {logo:"",name:"卡片名称10",type:"卡片类型",feature1:"信用卡特点1信用卡特点1信用卡特点1",feature2:"信用卡特点2信用卡特点2信用卡特点2",price:"30元",id:"14"}
                    ],
                pageNo:1,
                pageSize:7,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false
            }
        },
        methods:{
            getDetail(data){
               this.$router.push('/makemoney/banka/getcardmission/reservedInformation'+"/"+data.id)
            },
            loadBottom:function() {
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
		    	this.$http({
						method:'post',
						url:this.$taskSearch,
						 params:{
							 taskType:3,
							 "page.pagesize":this.pageSize,
							 "page.currentPage":this.pageNo
						 },
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((response)=>{
						if(response.data.data.length == 0){
							this.allLoaded = true; 
							this.isAll = show;
							
						}else{
							this.datalist = this.datalist.concat(response.data.data); 
						}
						
					}).catch((err)=>{
						console.log(err)
					})		
		   },
		   more(){
		   	this.pageNo++
		   	this.loadList(true);
		   }

        },
        mounted(){
            this.loadList(false);
        }
    }
</script>

<style lang="scss" scoped>
    .box_1{
        height: 80%;
        width: 100%;
        display: flex;
        overflow: hidden;
        .box_2{
            height: 100%;
            width: 100%;
            flex: 1;
            overflow: auto;
            padding-top: 0.31rem;
            .content{
                width: 100%;
                padding:  0.31rem 0.62rem;
                background: #ffffff;
                margin-bottom: 0.62rem;
                height: 5.36rem;
                display: flex;
                position: relative;
                .card{
                    width: 7.59rem;
                    height: 4.78rem;
                    border: .03125rem solid #959595;
                    img{height: 100%;width: 100%;}
                }
                .mid{
                    margin-left: .3125rem;
                    height: 100%;
                    .name{
                        display: flex;
                        height: 1.46875rem;
                        line-height: 1.46875rem;
                        font-size: .6875rem;
                        span{color: #010101}
                        p{width: 3.125rem;height: .75rem;line-height: .75rem;background: #9a9a9a;
                        color: #ffffff;margin-top: .36875rem;}
                    }
                    .feature{color: #9f9d9d;font-size: .625rem;margin-bottom: .1875rem;}
                }
                .price{
                    position: absolute;
                    right: 0.62rem;
                    top: 2.34375rem;
                    color: #f94b4b;
                }
            }
        }
    }
</style>


