	<template>
    <div class="my_all">
        <!-- 幕布 -->
        <div class="mubu" v-if="!isLogin">
            <p>你是第一次认识我嘛，选择以下任何一个选项都有见</p>
            <p>面礼哦~</p>
            <div class="btn">
                <div class="btn_a" @click="goRegist">A:是，我要现金</div>
                <div class="btn_b" @click="goLogin">B:否，我要优惠</div>
            </div>
        </div>

        <div class="my_top" v-if="isLogin">
            <div class="my_left">
                <div class="tou"><img :src=this.headPic></div>
                <div class="my_id">
                    <p>确认过眼神</p>
                    <p>ID {{this.userid}}</p>
                </div>
            </div>
            <div class="my_right">
                <p>我的资产</p>
                <div class="tixian">
                    <span>{{mybalance}}元</span>
                    <div class="btn" @click="tixian">提现</div>
                </div>
               
            </div>
        </div>

        <div class="my_main" v-if="isLogin">

            <div class="form">
            	<div @click="taskRecord"><span>任务记录</span><span>></span></div>
                <div @click="setting"><span>账户设置</span><span>></span></div>
                <div @click="about"><span>关于米多星</span><span>></span></div>
                <div @click="help" style="border-bottom:none;"><span>帮助中心</span><span>></span></div>
            </div>
        </div>
    </div>

</template>

<script>
    export default {
        name:"MyInfo",
        data(){
            return{
                isLogin:true,
                mybalance:'',
                userid:'',
                phone:"",
                headPic:''

            }
        },
        methods: {
            goRegist(){
                this.$router.push("/register")
            },
            goLogin(){
                this.$router.push("/Login")
            },
            //提现
            tixian(){
                this.$router.push("/my/drawCash")
            },
            //去使用
            goUse(){
//              this.$router.push("/my/coupon")
            },
            //任务记录
            taskRecord(){
                this.$router.push('/my/setting/TaskRecordMain')
            },
            //优惠记录
            discountsRecord(){
                
                this.$router.push('/my/setting/DiscountRecordMain')
            },
            //账户设置
            setting(){
				this.$router.push("/my/setting")
            },
            //关于xxx
            about(){
				this.$router.push('/my/aboutUs')
            },
            //帮助中心
            help(){
                this.$router.push({
                	path:"/my/helpcenter",
                	query:{
                		phone:this.phone
                	}
                	})
            }
        },
        beforeMount(){
            //判断是否登录
         if(localStorage.getItem("userId")){
             this.isLogin = true;
//           this.mybalance = localStorage.getItem("userId")
//           this.mybalance = JSON.parse(this.mybalance).balance
             this.userid = JSON.parse(localStorage.getItem("userId")).id

             this.phone = localStorage.getItem("userId")
             this.phone = JSON.parse(this.phone).phone
            this.headPic = JSON.parse(localStorage.getItem("userId")).headPic
            this.headPic = "http://"+this.headPic
         }else{
             this.isLogin = false;
         }
        },
        mounted(){       	
       		this.$http({
       			method:"POST",
       			url:this.$getBalance,
       			params:{
       				phone:this.phone
       			}
       		}).then((res)=>{
       			this.mybalance = res.data.data
       		})
       
        }
    }
</script>

<style lang="scss" scoped>

    .my_all{
        height: 100%;
        width: 100%;
        background: #ededed;
        overflow: hidden;
        .mubu{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.45);
            z-index: 160;
            padding-top: 12.8125rem;
            p{height: 1.25rem;line-height: 1.25rem;font-size: .875rem;
                color: #ffffff;margin-left: 1.71875rem;}
                .btn{margin-top: 2.6875rem;margin-left: 1.71875rem;display: flex;
                        div{height: 1.90625rem;line-height: 1.90625rem;text-align: center;width: 8.28125rem;
                            background: #ff4f00;border-radius: .3125rem;color: #ffffff;font-size: .8125rem;}
                            .btn_b{margin-left: 3.5rem;}
                    }
        }
        .my_top{
            height: 9rem;
            width: 100%;
            background: #fae44c;
            padding: 0 .625rem;
            display: flex;
            .my_left{height: 4.62rem;width: 8.62rem;
            border-right: .03125rem solid #f94b4b;
                    margin-top: 2.43rem;
                    display: flex;
                    .tou{height: 3.5rem;
                    	width: 3.5rem;
                    	border: 2px solid #000000;
                    	border-radius: 50%;
                    	background: #ffffff;
                    	text-align: center;
                    	line-height: 4.1rem;
                    	overflow: hidden;
                        img{height: 100%;width: 85%;}
                        }
                    .my_id{
                            padding-top: .78125rem;padding-left:.4125rem;
                            p{line-height: 1.5625rem;font-size: .84375rem;color: #71473a;}
                         }
                    }
            .my_right{
               flex: 1;
               height: 100%;
               padding-top: 2.25rem;
               font-size: .875rem;
               padding-left:.78125rem;
                p{
               text-align: left;
               	color: #71473a;
               	font-size: 1.06rem;
               	padding-left: 0.62rem;
               	}
                .tixian{
                    height: 3.75rem;
                    line-height: 1.5rem;
                    width: 100%;
                    display: flex;
                    position: relative;
                    
                    span{color: #ff0101;
                    	display: block;
                    	width: 6rem;
                    	height: 3.75rem;
                    	text-align: center;
                    	line-height: 4.00rem;
                    	font-size: 1.12rem;
                    }
                    .btn{position: absolute;
                    top: 1.46rem;
                    right: 0;
                    height: 1.5rem;
                    width: 3.34375rem;
                    text-align: center;
                    line-height: 1.5rem;
                    color: #ffffff;
                    background: #f9714b;
                    border-radius: .21875rem;
                       }
                }
            }
        }
        .my_main{
            height: 100%;
            width: 100%;
            .jilu{
                height: 6.96875rem;width: 100%;padding-left: .71875rem;display: flex;margin-top:1.625rem;
                .box{height: 6.96875rem;width: 6.96875rem;background: #f94b4b;margin-left: 2.71875rem;
                    img{height: 4.0625rem;width: 4.46875rem;margin-top: .875rem;margin-left: 1.28125rem;}
                    p{width: 100%;text-align: center;height: 2.0625rem;line-height: 2.0625rem;color: #ffffff;}
                    }
            }
            .form{
                margin-top: 1.5625rem;height: 9.375rem;width: 100%;padding: 0 .625rem;
                div{
                    width: 100%;height: 3.125rem;line-height: 3.125rem;font-size: .875rem;color:#000000;
                    border-bottom: .03125rem solid #a6a6a6;background: #ffffff;
                    span:nth-child(1){
                        font-size: .90625rem;margin-left: 1.03125rem;
                    }
                    span:nth-child(2){
                        font-size: .90625rem;float: right;margin-right: 1.09375rem;
                    }
                }
            }
        }
    }
</style>


