<template>
	<div class="suggestplay">
		<div class="form_header">
			<div class="back" @click="back"></div>
			<div class="center">推荐试玩</div>
			<div class="block"></div>
		</div>
		<div class="order">
			<div class="order1">完成任务</div>
			<div class="order2">审核通过</div>
			<div class="order3">获得奖励</div>
		</div>
		<div class="mession_tab">
			<div @click="to_messionlist" class="listBorder">
				<div v-show="show1" class="show1"><span>任务列表</span></div>
				<div v-show="show2" class="show2"><span>任务列表</span></div>				
			</div>
			<div :class="[isOk?'my_mession':'my_mession2']" @click="to_mymession"><span>参与任务</span></div>
		</div>
		<router-view></router-view>
	</div>
</template>

<script>
	export default{
		name:"FormRegister",
		data(){
			return{
				isOk:true,
				show1:true,
				show2:false
					}
			
		},
		methods:{
			back(){
				this.$router.push('/makemoney/jisu')
			},
			to_mymession(){
				this.isOk = false,
				this.show1=false,
				this.show2=true,
				this.$router.push('/makemoney/suggestplay/mysuggestplay')
			},
			to_messionlist(){
				this.show1=true,
				this.show2=false,
				this.isOk = true,
				this.$router.push('/makemoney/suggestplay/suggestplayList')
			}
		},
		updated(){
			if(this.$route.name == 'suggestplayList'){
				this.show1=true,
				this.show2=false,
				this.isOk = true
			}else{
				this.isOk = false,
				this.show1=false,
				this.show2=true
			}
		}
	}
</script>

<style lang="scss" scoped>
  .suggestplay{
  	width: 100%;
  	height: 100%;
  	background: #ffffff;
	overflow: hidden;
  
.form_header{
  	width: 100%;
	height: 2.85rem;
  	background: #fae44c;
  	padding-left: 0.62rem;
  	padding-right: 0.62rem;
  	box-sizing: border-box;
  	display: flex;
  	justify-content: space-between;
  	 .back{
  	width: 2.62rem;
  	height: 100%;
  	background: url(../../../assets/back.png) no-repeat center center;
  	background-size: 0.62rem 1.28rem;
 		 }
 		  .center{
  	width: 7.61rem;
  	height: 2.81rem;
  	text-align: center;
  	line-height: 2.81rem;
  	font-size: 1.12rem;
    color: #72463d;
  }
  .block{
  	width: 2.62rem;
  }
 	 }
 	 
   .order{
   	width: 100%;
   	height: 2.18rem;
   	padding: 0 0.62rem;
   	background: #d5d5d5;
   	display: flex;
   	justify-content: space-between;
   	align-items: center;
   	div{
   		height: 1.21rem;
   		text-align: center;
   		line-height: 1.21rem;
   		color: #ffffff;
   		font-size: 0.68rem;
   	}
   	.order1{
   		width: 4.78rem;
   		background: url(../../../assets/order1.png) no-repeat center center;
   		background-size: 100% 100%;
   	}
   	.order2{
   		width: 4.75rem;
   		background: url(../../../assets/order2.png) no-repeat center center;
   		background-size: 100% 100%;
   	}
   	.order3{
   		width: 4.93rem;
   		background: url(../../../assets/order3.png) no-repeat center center;
   		background-size: 100% 100%;
   	}
   }
   .mession_tab{
   	width: 100%;
   	height: 2.06rem;
   	display: flex;
   	border-bottom: 1px solid #999999;
   	background: #ffffff;
   	.listBorder{
   		border-right:1px solid #999999;
   		height:100%;
	   	.show1{
	   		width: 16.06rem;
	   		height: 2.06rem;			
	   		span{
	   			width: 3.93rem;
	   			height: 93%;
	   			display: block;
	   			margin: 0 auto;
	   			line-height: 2.06rem;
		   		font-size: 0.53rem;
		   		color: #f94b4b;
		   		text-align: center;
		   		border-bottom: 1px solid #f94b4b;
	   		}
	   	}
	   	.show2{
	   		width: 7.4rem;
	   		height: 100%;
	   		text-align: center;
	   		line-height: 2.06rem;
	   		text-align: center;
	   		background: #ffffff;
	   		color: #999999;
	   		font-size: 0.53rem;
	   	}
   	}
    .my_mession{
    	flex: 1;
    	text-align: center;
    	line-height: 2.06rem;
    	color: #999999;
    	font-size: 0.46rem;
    	
    }
    .my_mession2{
    	flex: 1;
    	height: 100%;
    	span{
	   			width: 3.93rem;
	   			height: 97%;
	   			display: block;
	   			margin: 0 auto;
	   			line-height: 2.06rem;
		   		font-size: 0.53rem;
		   		color: #f94b4b;
		   		text-align: center;
		   		border-bottom: 1px solid #f94b4b;
	   		}
    }
   }
 }	

</style>



