<template>
    <div class="bind_wx">
        <div class="wx_header">
			<div class="back" @click="wx_back"></div>
			<div class="center">微信绑定</div>
			<div class="block"></div>
		</div>
        <div class="qrcode"></div>
        <div class="qrname">xxx公众号</div>
        <div class="goFocus" @click="goFocus"><p>前往关注</p></div>
    </div>
</template>

<script>
    export default {
        name:"wxBind",
        data(){
			return{}
		},
		methods:{
			wx_back(){
				this.$router.go(-1)
            },
            goFocus(){
                console.log("去关注")
            }
		}
    }
</script>

<style lang="scss">
    .bind_wx{
	width: 100%;
	height: 100%;
	background: #f8f8f8;
	.wx_header{
			  	width: 100%;
	height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 		  .center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 }
      .qrcode{
          height: 6.28125rem;
          text-align: center;
          margin-top: 2.65625rem;
      }
      .qrname{
          height: 2.8125rem;
          line-height: 2.8125rem;
          text-align: center;
          font-size: .9375rem;
          margin-bottom: .78125rem;
      }
      .goFocus{
          height: 1.75rem;
          width: 100%;
          text-align: center;
          p{display: inline-block;
            width: 10.28125rem;
            height: 1.75rem;
            line-height: 1.75rem;
            background: #f94b4b;
            color: #ffffff;
            font-size: .9375rem;
            border-radius: .31875rem;
            }
      }
    }
</style>


