<template>
<!--手机换绑短信验证之前手机-->
<div class="changeBind">
	<div class="changeBind_header">
			<div class="back" @click="changeBind_back"></div>
			<div class="center">手机换绑</div>
			<div class="block"></div>
	</div>
	<div class="warn1">为了账户安全，需要验证当前手机的有效性</div>
	<div class="thisNumber">当前绑定手机号<span>{{phoneNumber}}</span></div>
	<div class="sendMessege">
		<input type="text" id="changeBind_yanZ" placeholder="输入短信验证码" v-model="validateCode"/>
		<div class="get_code">
					<span v-show="show" @click="getCode">获取验证码</span>
					<span v-show="!show" class="count">约{{count}}s后获取</span>
		</div>
	</div>
	<!-- <div class="another">
		无法接受短信？<span @click="to_another">其他验证放方式</span>
	</div> -->
	<div class="changeBind_nextStep" @click="to_bindNewPhone"><span class="submit_button">下一步</span></div>	

</div>
</template>

<script>

import { Toast } from 'mint-ui';

	export default{
		data(){
			return{
				// 隐藏的手机号
				phoneNumber:"",
				// 未隐藏的
				phone_number:'',
				show: true,
				validateCode:"",
				count: ''
			}
		},
		methods:{
			changeBind_back(){
				this.$router.go(-1)
			},
			getCode(){
						//进行短信验证
						const baseUrl = this.$ChangeBind_Msg+this.phone_number;
						this.$http({
						method:'get',
						url:baseUrl,
						  headers: {
							"Accept" : "*/*"
						  },
					}).then((response)=>{
						console.log(response)
					
					}).catch((err)=>{
						console.log(err)
					})
					

				const TIME_COUNT = 60;
						if (!this.timer) {
						this.count = TIME_COUNT;
						this.show = false;
						this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= TIME_COUNT) {
							this.count--;
							} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
							}
						}, 1000)
						}
			},
			to_bindNewPhone(){				
//			判断验证码输入不为空
			 if(this.validateCode==""){
						let instance = Toast('请输入验证码');
						setTimeout(() => {
						instance.close();
						}, 2000);
						return;
			 }else{
				 //请求旧手机接口判断输入是否正确，  然后跳转
						 this.$http({
						method:'post',
						url:this.$ChangeBindOld+"?phone="+this.phone_number+"&validateCode="+this.validateCode,
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((response)=>{
						console.log(response)
						let instance = Toast(response.data.resultMessage);
						setTimeout(() => {
						instance.close();
						}, 2000);
						if(response.data.resultCode == 0){
							var timers = setTimeout(()=>{  
										this.$router.push("/my/setting/verifyNumber/ChangeBind/BindNewPhone") 		
								},2000);  
							
							}
						}).catch((err)=>{
						console.log(err)
						})
					}
			}

		},
		  updated () {
		if(this.validateCode!==""){
					 document.getElementsByClassName("submit_button")[0].style.background="#f94b4b";
					 document.getElementsByClassName("submit_button")[0].style.color="#ffffff";
		  }else{
			  document.getElementsByClassName("submit_button")[0].style.background="#c1c1c1";
		  }  
	  },
	  beforeMount(){

		  	//读取缓存登录手机号码
			var phone_number = localStorage.getItem("userId");
			  phone_number = JSON.parse(phone_number).phone
				this.phone_number = phone_number;

			//隐藏手机号中间4位	
			var hide_phone_number = phone_number.substr(0,3)+"****"+phone_number.substr(7); 
			this.phoneNumber = hide_phone_number
		}
	}
</script>

<style lang="scss" scoped>
	.changeBind{
		width: 100%;
		height: 100%;
		background: #f8f8f8;
		.changeBind_header{		
			  	width: 100%;
	height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
		}
		.warn1{
			width: 100%;
			height: 1.34rem;
			line-height: 1.34rem;
			color: #f94b4b;
			font-size:0.71rem;
			padding-left: 0.62rem; 
		}
		.thisNumber{
			width: 100%;
			height: 2.62rem;
			line-height: 2.50rem;
			font-size: 0.68rem;
			color: #5a5a5a;
			padding-left: 0.62rem;
			span{
				color: #181818;
				margin-left: 0.93rem;
			}
		}
		.sendMessege{
			width: 100%;
			height: 1.56rem;
			padding: 0 0.62rem;
			display: flex;
			#changeBind_yanZ{
				width: 17.43rem;
				height: 100%;
				display: block;
				border: none;
				background: #f8f8f8;
				border-bottom: 0.03rem solid #c1c1c1;
				
			}
			#changeBind_yanZ::-webkit-input-placeholder{
				color:#c6c6c6;
				font-size: 0.65rem;
			}
			.get_code{
				flex: 1;
				height: 100%;
				background:#fae44c;
				font-size: 0.68rem;
				color: #000000;
				text-align: center;
				line-height: 1.56rem;
				border-radius: 5px;
			}
		}
		// .another{
		// 	width: 100%;
		// 	height: 2.12rem;
		// 	padding-left: 0.62rem;
		// 	line-height: 2.12rem;
		// 	color:#181818;
		// 	font-size: 0.65rem;
		// 	span{
		// 		color: #f94b4b;
		// 	}
		// }
		.changeBind_nextStep{
			width: 100%;
			height:2.03rem;
			padding: 0 0.62rem;
			span{
				display: block;
				text-align: center;
				width: 100%;
				height: 100%;
				line-height: 2.03rem;
				font-size: 0.81rem;
				color: #ffffff;
				background: #e2e2e2;
				margin-top: 1.09rem;
				border-radius: 5px;
			}
		}
		.err_num{
	  position: absolute;
	  top: 9rem;
	  left: 7rem;
	  height: 3.3375rem;
	  line-height: 3.3375rem;
	  text-align: center;
	  font-size: 0.3rem;
	  border-radius: .4125rem;
	  width: 9.5625rem;
	  background: rgba(0, 0, 0, .8);
	  color: #ffffff;
	  
  }
	}
</style>


