<template>
    <div class="The_third">
        <div class="third_header">
			<div class="back" @click="back">返回</div>
			<div class="center">{{this.title}}</div>
			<div class="block"></div>
		</div>
        <iframe :src=this.src height="100%" width="100%"></iframe>
    </div>
</template>

<script>
    export default {
        name:'',
        data(){
            return{
                title:'',
                src:''
            }
        },
        methods:{
            back(){
                this.$router.go(-1)
            }
        },
        beforeMount(){
            this.title = this.$route.query.title
            this.src = this.$route.query.url
        }
    }
</script>

<style lang="scss">
.The_third{
	height: 100%;
	width: 100%;
	/*overflow: hidden;*/
	overflow-x: hidden;
	iframe{
  	margin-top:4.03rem;
  }
	}
    .third_header{
    position: fixed;
    top: 0;
  	width: 100%;
	height: 2.85rem;
  	background: #363636;
  	padding-left: 0.62rem;
  	padding-right: 0.62rem;
  	box-sizing: border-box;
  	display: flex;
  	justify-content: space-between;
  	 .back{
  	width: 3.62rem;
  	height: 100%;
    line-height: 2.84rem;
  	font-size: 1.0625rem;
    color: #ffffff;
    padding-left: .8125rem;
  	background: url(../assets/back_w.png) no-repeat center left;
  	background-size: 0.62rem 1.28rem;
 		 }
 		  .center{
  	width: 6.61rem;
  	height: 2.81rem;
  	text-align: center;
  	line-height: 2.81rem;
  	font-size: 1.12rem;
    color: #ffffff;
  }
  .block{
  	width: 3.62rem;
  }
  
 	 }
</style>


