<template>
	<div class="myTask_in">
			<div class="noTask" v-if="isNothing">暂无参与注册任务</div>
			
		<v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
			<div class="Task_in_main" v-if="!isNothing" v-for="tasklist in myTask" :key="tasklist.id">  
				<div class="Task_top">
					<div class="Task_logo"><img :src="'http://'+tasklist.taskLogo" alt=""></div>
					<div class="Task_info">
						<div class="top_info">
							<span>{{tasklist.taskName}}</span>
							<span>当前状态: {{tasklist.statusDepict}}</span>
						</div>
						<div class="reward" v-if="tasklist.status == 7">+{{tasklist.reward}}元</div>
						<div class="Task_time">参与时间： {{tasklist.partakeDate}}</div>
					</div>
				</div>
				<div class="Task_btm">
					<div class="Task_btn" v-if="tasklist.status == 1" @click="Task_award(tasklist)">领取奖励</div>
					<div class="Task_btn" v-if="tasklist.status == 1" @click="Task_goOn(tasklist)">继续任务</div>
					<div class="Task_btn" v-if="tasklist.status == 6" @click="Task_getCheck(tasklist)">查看原因</div>
				</div>
			</div>
			<div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../../assets/cry.png"/></div>
		</v-loadmore>	
			
			   <div class="prompt_box" v-if="isBoxShow"></div>
            <div class="box_list" v-if="isBoxShow">
                <div class="list_top">
                    <p>不通过原因</p>
                    <p>由于{{this.renson}}导致申请不通过，可以再次领取或重新注册</p>
                    <p>注意： xxx</p>
                </div>
                <div class="list_btm">
                    <div @click="list_reset">重做任务</div>
                    <div @click="re_apply">重新领取</div>
                </div>
             </div>
	</div>
</template>

<script>
// 表单注册任务参与任务模块
	import { Loadmore } from 'mint-ui';
	export default{
		name:"MyFromMession",
        components:{
    	  'v-loadmore':Loadmore,
  		  },
		data(){
			return{
				//参与任务是否为空
				isNothing:"",
				//弹窗是否显示,
				isBoxShow:"",
				//失败原因
				renson:'xxxxxxxxxxx',
				id:'',
				myTask:[],
                pageNo:1,
                pageSize:4,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false,
                userId:''
			}
		},
		methods: {
			//领取奖励   跳转领取奖励页面
			Task_award(tasklist){
				console.log(tasklist.id)
			 this.$router.push('/makemoney/jisu/fromRegister/formMessionList/sendPhoto')
				
			},
			//继续任务   跳转第三方做注册任务
			Task_goOn(tasklist){
				console.log(tasklist.id)
				
			},
			//查看原因
			Task_getCheck(tasklist){
				console.log(tasklist.id)
				this.id = tasklist.id;
				this.isBoxShow = true
			},
			//重做任务   跳转第三方做注册任务
			list_reset(){
				this.isBoxShow = false;
				console.log(this.id)
				
			},
			//重新领取   跳转领取奖励页面
			re_apply(){
				this.isBoxShow = false;
				console.log(this.id)
				this.$router.push('/makemoney/jisu/fromRegister/formMessionList/sendPhoto')
			},
            loadBottom:function() {
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
		    	this.$http({
						method:'POST',
						url:this.$taskRecordSearch,
						headers: {
   							 "content-type": "application/json"
						 },
						responseType:'text/plain',
						 params:{
						 		userId: this.userId,
						 		 taskType:1,
							 	 "page.pagesize":this.pageSize,
							 	 "page.currentPage":this.pageNo
						 }
			}).then((res)=>{
				if(res.data.data.length == 0){
					this.allLoaded = true; 
					this.isAll = show;
					//当接收的数据为空时，判断列表是否也为空
				    if(this.myTask.length == 0){
					this.isNothing = true;
					}else{
					this.isNothing = false;
					}
				}else{
					this.myTask = this.myTask.concat(res.data.data)
				}
				})	
		   },
		   more(){
		   	this.pageNo++
		   	this.loadList(true);
		   }
		},
		beforeMount () {
			let user = localStorage.getItem("userId")
			if(user){
				user = JSON.parse(user)
			this.userId = user.id
			console.log(user.id)
			this.loadList(false)
			}else{
				this.isNothing = true
			}
			

		}
			
	
	}
</script>

<style lang="scss" scoped>
	.myTask_in{
		width: 100%;
		padding-top:.6875rem;
		padding-bottom: 1.5rem;
		 .prompt_box{
            height: 100%;width:100%;position: absolute;background: rgba(0, 0, 0, 0.45);top: 0;}
            .box_list{
                height: 15.40625rem;width: 14.75rem;
                position: absolute;    
                 top: 35%;left: 19%;
                background: url("../../../../assets/tanchuang.png") no-repeat center center;
				background-size: 14.75rem 15.40625rem;
				border-radius: .5625rem;
                .list_top{height: 7.5625rem;width: 100%;padding: 0 1rem;
                        p:nth-child(1){height: 1.375rem;line-height: 1.375rem;font-size: .6875rem;color: #ffffff;font-size: .9375rem;
                                        margin-bottom: .3rem;text-align: center;margin-top: 5.03125rem;}
                        p:nth-child(2){width: 100%;overflow: hidden;font-size: .6875rem;line-height: 1.125rem;margin-top: 1.3rem;margin-bottom: .7rem;}
                        p:nth-child(3){font-size:.5625rem;line-height: 1rem;color: #ff0000;}
                        }
                .list_btm{display: flex;
                        div{width: 50%;text-align: center;height: 1.84375rem;line-height: 2.84375rem;}
                        div:nth-child(1){}
                        }

            }
		.noTask{

			height: 50%;

			width: 100%;
			position: absolute;
    		top: 50%;
    		text-align: center;
		}
		.Task_in_main{
			position: relative;
			background: #ffffff;
			height: 7.84375rem;
            width: 100%;
            border-top: .03125rem solid #e1e1e1;
        	box-shadow: 0rem 0.215rem 0.625rem #888686;
            border-bottom: .03125rem solid #6a6969;
            margin-bottom: .46875rem;
            padding: 0 .625rem;
			.Task_top{
				height: 5.40625rem;width: 100%;
                border-bottom: .03125rem solid #535353;
                margin-top: .3125rem;
                display: flex;
				.Task_logo{height: 4.4375rem;width: 4.4375rem;
						img{height: 100%;width: 100%;}
						}
				.Task_info{height: 100%;width: 78.5%;margin-left: .3125rem;
						.top_info{margin-top: .5625rem;font-size: .8125rem;
								 span:nth-child(1){color: #000000;float: left;}
								 span:nth-child(2){color: #f94b4b;float: right;}
								 }
						.Task_time{margin-top: 3.40625rem;font-size: .8125rem;color: #3d3d3d;color: #3d3d3d;}
						.reward{    position: absolute;right: 2rem;top: 2.5rem;color: #000000;font-size: 1rem;}
						 }
			}
			.Task_btm{
				height: 1.5625rem;width: 100%;margin-top: .3rem;
				.Task_btn{
					float: right;margin-left: .9375rem;border-radius: .21875rem;
					height: 1.375rem;line-height: 1.375rem;width: 3.4375rem;text-align: center;
                    font-size: .75rem;color: #ffffff;background: #f94b4b;
				}
			}
		}
	}
</style>


