<template>
	<div class="makemoney">
		<!--点击领取新人红包-->
			  <div class="newRedBox" v-show="redShow">
				  <div class="getmoney" @click="get_redbox" >
					<img src="../../assets/smoll_redbox.png"/>
					<span>新人红包</span>
				</div>
				<!--红包弹窗-->
				<div class="hongbao" v-show="redbox">
					 <div class="big_redbox">
					 	<div class="cha" @click="cha"></div>
					 	<div class="redbox_tologin" @click="redbox_tologin"></div>
					 </div>
					 
				</div>
			  </div>
				
		<!--头部分-->
		<div class="mak_header">
			<div class="left">
				<div class="myZc">我的资产</div>
				<div class="logined" v-if="islogin">
					<div class="mybalance">{{mybalance}}元</div>
					<div class="toDrowCash"></div>
				</div>
				<div class="beforeLogin" v-if="!islogin" @click="to_login"></div>
			</div>
			<div class="right">
				<div class="try" @click="to_suggest">推荐试玩</div>
				<div class="sign" @click="to_third">签到送惊喜</div>
			</div>
		</div>
		
		<!--banner-->
		<div class="makemoney-banner">
			<img src="../../assets/index_banner.png"/>
		</div>
		<!-- 轮播图下的任务块 -->
		<div class="main_jisu">
			<!-- 顶部任务按钮 -->
			<div class="main_top">
				<router-link tag="div" :to="'/makemoney/jisu'"><span>注册任务</span></router-link>
				<router-link tag="div" :to="'/makemoney/Download'"><span>下载任务</span></router-link>
				<router-link tag="div" :to="'/makemoney/banka'"><span>办卡任务</span></router-link>
				<!-- <div :class="[ys1?'show':'']" @click="jisu">极速任务</div>
				<div :class="[ys2?'show':'']" @click="banka" style="border-right:none">办卡任务</div>
				<div :class="[ys3?'show':'']" @click="taobao" v-if="false">淘宝刷单</div> -->
			</div>
			<!-- 路由内容部分 -->
			<div class="main_bottom">
					<router-view @to-parent="getChild"></router-view>
			
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:"MakeMoney",
		data(){
			return{
				title:"米多星",
				ys1:true,
				ys2:false,
				ys3:false,
				 redbox:false,
				 midi:"登录揭开谜底",
				 //是否显示新人红包状态
				 redShow:'',
				 //是否登录
				 islogin:'',
				 //我的资产
				 mybalance:'',
				 //缓存手机号码
				 phone:'',
				 //签到送惊喜的第三方url
				 thirdUrl:'http://activity.buqec.cn/activity/index?id=6401&slotId=1625&login=normal&appKey=3m42Vi3ab5RPubq9LPJBtKsZ2X8o&deviceId=106050e2-b454-4dcb-a5e3-243224cfc410&dsm=1.1625.0.0&tenter=SOW&tck_rid_6c8=0a141e62jggj50ti-6842897&tck_loc_c5d=tactivity-6401&dcm=401.1625.0.0'

			}			
		},
		methods: {
			get_redbox(){
  				this.redbox=true
  			},
  			//点击叉叉让红包消失
	  	cha(){
	  		this.redbox = false
	  	},
	  	//点击领取红包跳转到登录页面
	  	redbox_tologin(){
			  // this.$router.push("/login")
			  this.$router.push('/register')
		  },
		  to_login(){
			  this.$router.push("/login")
		  },
		  //未登录时,办卡的筛选框出来，隐藏红包
		  getChild(data){
			  if(!localStorage.getItem("userId")){
				if(data){
					this.redShow = true
				}else{
					this.redShow = false
				}
			  }

		},
		//去签到送惊喜
		to_third(){
			if(!localStorage.getItem('userId')){
				this.$router.push("/login")
			}else{
				this.$router.push({path:'/third',query:{title:'签到送惊喜',url:this.thirdUrl}})
			}
		},
		to_suggest(){
			this.$router.push('/makemoney/suggestplay')
		}
	  	
		},
		
		beforeMount(){
       	  //渲染前判断是否领过新人红包
       	  if(localStorage.getItem("userId")){
				 this.islogin = true
				 this.redShow = false
//				 this.mybalance = localStorage.getItem('userId')
//				 this.mybalance = JSON.parse(this.mybalance).balance
				 this.phone = localStorage.getItem('userId')
				 this.phone = JSON.parse(this.phone).phone;
				 
       	  }else{
				 this.islogin = false
				 this.redShow = true
			 }
       },
       mounted(){
       		this.$http({
       			method:"POST",
       			url:this.$getBalance,
       			params:{
       				phone:this.phone
       			}
       		}).then((res)=>{
       			this.mybalance = res.data.data
       		})
       }
	}
</script>

<style lang="scss" scoped>

.makemoney{
	width: 100%;
	height: 100%;
	overflow: hidden;
	background: #ffffff;
	
	.mak_header{
		width: 100%;
		height: 6.18rem;
		background: url(../../assets/makHeaderBg.png) no-repeat center center;
		background-size: 100% 100%;
		color: #fffffd;		
		display: flex;
		.left{
			width:13.43rem;
			height: 100%;
			border-right: 1px solid #ffffff;
			.myZc{
				width: 100%;
				height: 1.9rem;
				line-height: 1.9rem;
				font-size: 0.93rem;
				text-align: center;
				background: url(../../assets/moneyBox.png) no-repeat 3.12rem center;
				background-size: 1.12rem 1.21rem;
			}
			.logined{
				width: 100%;
				height: 4.28rem;
				.mybalance{
					width: 100%;
					height: 2rem;
					text-align: center;
					font-size: 1.68rem;
					line-height: 2rem;
				}
				.toDrowCash{
					width: 4.62rem;
					height: 1.68rem;
					margin: 0.31rem auto;					
					background: url(../../assets/cashButton.png) no-repeat center center;
					background-size: 100% 100%;
				}
			}
			.beforeLogin{
				width: 7.75rem;
				height: 2.06rem;
				margin: 0.93rem auto;
				background: url(../../assets/makToLogin.png) no-repeat center center;
				background-size: 100% 100%;
			}
		}
		.right{
			flex: 1;
			height: 100%;
			padding-right: 0.62rem;
			font-size: 0.93rem;			
			.try{
				width: 100%;
				height: 3.12rem;
				padding-left: 4.03rem;
				background: url(../../assets/giftBox.png) no-repeat 1.59rem 0.90rem;
				background-size: 1.28rem 1.18rem;
				line-height: 3.12rem;
				border-bottom: 1px solid #ffffff;
			}
			.sign{
				width: 100%;
				height: 3.03rem;
				padding-left: 4.03rem;
				line-height: 3.03rem;
				background: url(../../assets/smileBox.png) no-repeat 1.59rem 0.90rem;
				background-size: 1.25rem 1.25rem;
			}
		}
	}
		.makemoney-banner{
			width: 100%;
			height: 8.78rem;
			margin-top: 0.31rem;
			img{
				width:100%;
				height: 100%;
			}
		}
		.main_jisu{
			height:22.18rem;
			width: 100%;
			background: #ffffff;
			display: flex;
			flex-direction: column;
			overflow: hidden;
			.main_top{
				width:100%;
				height: 2.03rem;
				padding: 0 0.62rem;
				justify-content: space-between;
				border-bottom: 1px solid #b0b0b0;
				display:flex;
				
				.router-link-active{
			        color:#f6724b;
			        span{
			        	width: 3.87rem;
			        	height: 99%;
			        	display: block;
			        	border-bottom: 1px solid red;
			        	text-align: center;
			        	margin: 0 auto;
			        }	
		            }
				div{
					height: 100%;		
					flex: 1;
					line-height: 2.03rem;
					text-align: center;
					color: #3b3b3b;
					font-size:0.87rem;
					border-right: 1px solid #e5e5e5;
				}
				div:nth-child(3){
					border: 0;
				}
			
			}
			.main_bottom{
				height: 17.80rem;
				width: 100%;
				background: #ffffff;
				flex: 1;
				/*overflow-x: hidden;*/
				overflow: hidden;
			}
		}
}

.getmoney{
		width: 3.00rem;
		height: 2.71rem;
		position: fixed;
		right: 0.62rem;
		bottom: 8.46rem;
		text-align: center;
		z-index: 150;
		img{
			width: 1.5rem;
			height: 1.78rem;
		}
		span{
			display: block;
			width: 100%;
			height: 0.87rem;
			text-align: center;
			line-height: 0.87rem;
			font-size: 0.56rem;
			margin-top: .21875rem;
		}
	}
	.hongbao{
		position: fixed;
		top: 4.00rem;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.45);
		z-index: 160;
		.big_redbox{
			margin: 6.06rem auto;
			width: 18.62rem;
			height: 19.68rem;
			background: url(../../assets/big_redbox.png) no-repeat center center;
			background-size: 100% 100%;
			overflow:hidden;
			.cha{
				width: 2.18rem;
				height: 2.18rem;
				margin-top: 4.87rem;
				margin-left: 14.68rem;
			}
			.redbox_tologin{
				width: 7.75rem;
				height: 2.09rem;
				margin-top: 8rem;
				margin-left: 6.5rem;
			}
		}
	}



</style>


