<template>
	<div class="helpcenter">
		<div class="help_header">
			<div class="back" @click="help_back"></div>
			<div class="center">帮助中心</div>
			<div class="block"></div>
		</div>
		<div class="header_bm">
            <div>Hi,{{this.phone}}</div>
            <p>已为您定制如下内容</p>
            <p>您的每一次到来都有“在线客服”的贴心陪伴</p>
        </div>
        <iframe src="http://cloud.hzqimiao.com:8099/help" height="100%" width="100%"></iframe>
        <div class="more_help">更多帮助</div>
        <div class="contact">
            <div class="contact_btn">
                <div class="btn_content" @click="contactKf">
                    <div class="btn_left"></div><div class="btn_right">联系客服</div>
                    </div>
                </div>
        </div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				phone:""
			}
		},
		beforeMount(){
			this.phone = this.$route.query.phone
			console.log(this.phone)
		},
		methods:{
            help_back(){
                this.$router.push('/my')
            },
            //联系客服
            contactKf(){
                window.location.href = 'tel://'
            }
        }
	}
</script>
<style lang="scss" scoped>
	.helpcenter{
		width: 100%;
		height: 100%;
        background: #f2f2f2;
        display: flex;
        flex-direction: column;
        overflow: hidden;		
		.help_header{
			  	width: 100%;
			  	height: 3.4rem;                 
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 		  .center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
                 }
     .header_bm{
            height: 7.5rem;background: #fae44c;width: 100%;
            padding-left: .625rem;padding-top: 1.3125rem;
            div{color: #000000;font-size: 1rem;margin-bottom: .25rem;}
            p{font-size: .625rem;margin-bottom: 1.125rem;color: #000000;}
        }
        .more_help{height: 3.09375rem;line-height: 3.09375rem;font-size: .9375rem;color: #000000;margin-left: .625rem;}
        .contact{height: 2.5rem;width: 100%;margin: 0 .625rem;margin-bottom: 2.52rem;
                .contact_btn{height: 2.5rem;line-height: 2.5rem;text-align: center;width: 94.67%;
                            background: #f94b4b;border-radius: .3125rem;color: #ffffff;
                            .btn_content{height: 100%;width: 6.25rem;display: inline-block;
                                    .btn_left{height: 100%;width: 2.03125rem;float: left;background: url("../../assets/kefu.png") no-repeat center center;
                                                background-size:2.03125rem 1.875rem;}
                                    .btn_right{height: 100%;width: 4.21875rem;float: right;}
                                    }
                            }
                }
	}
</style>



