<template>
	<div class="myTask_in">
			<div class="noTask" v-if="isNothing">暂无参与下载任务</div>
	<v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
			<div class="join_task" v-if="!isNothing" v-for="tasklist in myTask" :key="tasklist.id">  
				<div class="join_task_top">
					<div class="join_task_logo"><img style="width: 100%;height: 100%;" :src="'http://'+tasklist.taskLogo" alt=""></div>
					<div class="join_task_info">
						<div class="join_task_topinfo">
							<span>{{tasklist.taskName}}</span>
							<span>当前状态： {{tasklist.statusDepict}}</span>
						</div>
						<div class="join_task_award" v-if="tasklist.award">
							+{{tasklist.reward}}
						</div>
						<div class="join_task_time">
							参与时间： {{tasklist.partakeDate}}
						</div>
					</div>
				</div>
				<div class="join_task_btm">
					<div class="join_task_btm_btn" v-if="tasklist.status==1" @click="joinTaskGoTask(tasklist)">继续任务</div>
				</div>
			</div>
			<div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../../assets/cry.png"/></div>
	</v-loadmore>			
	</div>
</template>

<script>
// 表单注册任务参与任务模块
	import { Loadmore } from 'mint-ui';
	export default{
		components:{
    	  'v-loadmore':Loadmore,
  		  },
		name:"MyFromMession",
		data(){
			return{
				//参与任务是否为空
				isNothing:"",
				myTask:[],
                pageNo:1,
                pageSize:4,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false,
                userId:''
			}
		},
		methods: {
			joinTaskGoTask(tasklist){
				console.log(tasklist.id)
				this.$router.push({path:'/downloadTaskDetail'+"/"+tasklist.id})
			},
			
            loadBottom:function() {
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
		    	this.$http({
						method:'POST',
						url:this.$taskRecordSearch,
						headers: {
   							 "content-type": "application/json"
						 },
						responseType:'text/plain',
						 params:{
						 		userId: this.userId,
						 		taskType:2,
							 	"page.pagesize":this.pageSize,
							 	"page.currentPage":this.pageNo
						 }
			}).then((res)=>{
				if(res.data.data.length == 0){
					this.allLoaded = true; 
					this.isAll = show;
					//当接收的数据为空时，判断列表是否也为空
				    if(this.myTask.length == 0){
					this.isNothing = true;
					}else{
					this.isNothing = false;
					}
				}else{
					this.myTask = this.myTask.concat(res.data.data)
				}
			   })
		   },
		   more(){
		   	this.pageNo++
		   	this.loadList(true);
		   }
            
		},
		beforeMount () {

		},
		mounted(){
			let user = localStorage.getItem("userId")
			user = JSON.parse(user)
			this.userId = user.id
			console.log(user.id)
			//参与任务列表
			
			this.loadList(false);
		    
		
			
		}
			
	
	}
</script>

<style lang="scss" scoped>
	.myTask_in{
		width: 100%;
		height: 80%;
		overflow: auto;
		padding-top:.6875rem;
		padding-bottom: 1.5rem;
		.noTask{
			height: 50%;

			width: 100%;
			position: absolute;
    		top: 50%;
    		text-align: center;
		}
		.join_task{
			height: 7.84375rem;
            width: 100%;
            border-top: .03125rem solid #e1e1e1;
        	box-shadow: 0rem 0.215rem 0.625rem #888686;
            border-bottom: .03125rem solid #6a6969;
            margin-bottom: .46875rem;
            padding: 0 .625rem;
			.join_task_top{
				height: 5.40625rem;width: 100%;
                border-bottom: .03125rem solid #535353;
                margin-top: .3125rem;
                display: flex;				
				.join_task_logo{height: 4.4375rem;width: 4.4375rem;
								img{height: 100%;width: 100%;}
								}
				.join_task_info{height: 100%;width: 78.5%;margin-left: .3125rem;position: relative;
				    .join_task_award{position: absolute;top: 1.875rem;right: 0;color: #000000;font-size: .875rem;}
					.join_task_topinfo{margin-top: .5625rem;font-size: .8125rem;
								 span:nth-child(1){color: #000000;float: left;}
								 span:nth-child(2){color: #f94b4b;float: right;}
								 }
					.join_task_time{margin-top: 3.40625rem;font-size: .8125rem;color: #3d3d3d;color: #3d3d3d;}
					}
			}
			.join_task_btm{
				height: 1.5625rem;width: 100%;margin-top: .3rem;
				.join_task_btm_btn{
					float: right;margin-left: .9375rem;border-radius: .21875rem;
					height: 1.375rem;line-height: 1.375rem;width: 3.4375rem;text-align: center;
                    font-size: .75rem;color: #ffffff;background: #f94b4b;					
				}
			}
		}
	}
</style>


