<template>
	<div class="phoneBill">
		<div class="weichatDrawCash_header">
			<div class="back" @click="weichatDrawCash_back"></div>
			<div class="center">话费充值</div>
			<div class="block"></div>
		</div>
		<div class="aliNumber">
			<span>手机号码</span>
			<input type="text" placeholder="请输入充值手机号码" v-model="billPhone"/>
		</div>
	
		<div class="chosseMoney">
			<div v-for="(list,index) in navLists" :key="list.text" class="nav" :class="{ red:changeRed == index}" @click="reds(index,list)">{{list.text}}</div>
		</div>
		<div class="jisuanti">
			<div v-show="rich" class="rich">余额<span>{{resum}}</span>元，充值<span>{{chooseMn}}</span>元，剩余<span>{{left}}</span>元</div>
			<div v-show="pool" class="pool">余额<span>{{resum}}</span>元，最低需要<span>15元</span><span @click="to_makemoney" class="to_makemoney">凑钱</span></div>
			<div v-show="pool2" class="pool">余额<span>{{resum}}</span>元，充值<span>{{chooseMn}}</span>元,余额不足<span @click="to_makemoney" class="to_makemoney">凑钱</span></div>
		</div>
		<div :class="[this.ok?'DrawCash_submit':'DrawCash_submit2']" @click="conferm_DrawCash">
			{{IsDrawCash}}
		</div>
		<p class="warm_warn">
			<span>温馨提示：</span> 提现后一个工作日到账，节假日延后。充值的金额请在手机话费账单中查询
		</p>
		<div class="sure_box"  v-show="areUsure">
			<div class="areUsure">
				<div class="top">确认信息</div>
				<div class="center">
					<p>充值账号：{{billPhone}}</p>
					<p>充值金额：{{this.chooseMn}}</p>
				</div>
				<div class="choises">
					<div class="yihou" @click="abandon">放弃</div>
					<div @click="to_apply">确认</div>
				</div>
			</div>
		</div>
		

	</div>
</template>

<script>
import { Toast } from 'mint-ui';
	export default {
		data(){
			return{
				//计算题里 显示对应状态
				rich:true,
				pool:false,
				pool2:false,
				navLists:[{"text":15},{"text":50},{"text":100}],
				changeRed:0,
				//当前账户余额
				resum:200,
				//当前选择提现的金额
				chooseMn:15,				
				//提现按钮展示的内容
				IsDrawCash:"充值申请",
				//三目判断按钮样式
				ok:true,
				//充值账号
				billPhone:"",
				//确认信息是否显示
				areUsure:false
			}
		},
		beforeMount(){
			//获取余额 判断显示能否取现
//      this.resum = JSON.parse(localStorage.getItem("userId")).balance
			this.resum = 60
		if(this.resum<15){
				this.rich = false;
				this.pool = true;
				this.ok = false;
				this.IsDrawCash = "余额不足，无法充值";
			}else{
				this.pool = false;
				this.rich = true;
				this.ok = true;
				this.IsDrawCash = "充值申请"
			}
		},
		methods:{
			weichatDrawCash_back(){
				this.$router.push('/my/drawCash')
			},
			to_makemoney(){
				this.$router.push('/makemoney/jisu')
			},			
			reds(index,list){
				this.changeRed = index;
				var htmltext = list.text
				this.chooseMn = htmltext
			},
			//点击充值按钮
			conferm_DrawCash(){
				var myreg=/^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;  
				//手机号码没有输入
				if(this.billPhone==""){
					let instance = Toast('请输入手机号');
					setTimeout(() => {
					instance.close();
					}, 2000);
					return;
				}else if(!myreg.test(this.billPhone)){
					let instance = Toast('请输入正确的手机号');
					setTimeout(() => {
					instance.close();
					}, 2000);
					return;
				}else if((this.resum - this.chooseMn)<0){	//判断余额是否足够
					this.areUsure = false
				}else{

				this.areUsure = true
				}

			},
			//确认信息 放弃按钮
			abandon(){
				this.areUsure = false
			},
			//确认 跳转到申请页面
			to_apply(){
				//请求 返回判断是否跳转
				this.$router.push({path:'/my/drawCash/submitted',query:{status:'phone',name:this.billPhone,price:this.chooseMn+"元",left:this.left+"元"}})
			}
		},
		computed:{
			left(){
				return this.resum - this.chooseMn
			}			
		},
		updated(){
			//判断提现金额大于余额的话 提示充不了			
			if(this.left<0){				
				this.ok = false;
				this.IsDrawCash = "余额不足，无法充值";
				this.rich = false;
				this.pool = false;
				this.pool2 = true;
			}else{
				this.ok = true;
				this.IsDrawCash = "充值申请";
				this.rich = true;
				this.pool = false;
				this.pool2 = false;
			}
			
		}
	}
</script>

<style lang="scss" scoped>
	.phoneBill{
		width: 100%;
		height: 100%;
		background: #f8f8f8;
		position: relative;
		.weichatDrawCash_header{					
			  	width: 100%;
	height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
		}
		.aliNumber{
			width: 100%;
			height: 1.25rem;
			padding: 0 0.62rem;
			margin-top: 2.37rem;
			display: flex;
			color: #181818;
			font-size: 0.71rem;
			span{
				 display: block;
				 width: 4.84rem;
				 height: 100%;
				 
			}	
			input{
				flex: 1;
				height: 100%;
				display: block;
				border: none;
				background: #f8f8f8;
				border-bottom: 1px solid #c1c1c1;
			}	
			input::-webkit-input-placeholder{
				color: #d4d4d4;
			}	
		
			
		}
		.chosseMoney{
			margin-top: 1.34rem;
			width: 100%;
			height: 5.71rem;
			padding: 0 0.62rem;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: flex-start;
			.nav{
				width: 5.68rem;
				height: 3.37rem;
				border: 0.03rem solid #666666;
				text-align: center;
				line-height: 3.37rem;
				color: #434343;
			}
			.red{
				border: 0.03rem solid #f94b4b;
			}
		}
		.jisuanti{
			width: 100%;
			height: 3.12rem;
			.rich{
				width: 100%;
				height: 100%;
				text-align: center;
				line-height: 3.12rem;
				color: #5a5a5a;
				font-size: 0.68rem;
				span{
				color:#ff0000 ;
				}
			}
			.pool{
				width: 100%;
				height: 100%;
				padding-left: 5.25rem;
				line-height: 3.12rem;
				color: #5a5a5a;
				font-size: 0.68rem;
				position: relative;
				span{
				color:#ff0000 ;
			}
				.to_makemoney{
					display:block;
					width: 2.81rem;
					height: 0.93rem;
					background: #f94b4b;
					text-align: center;
					line-height: 0.93rem;
					color: white;
					border-radius: 3px;
					font-size: 0.68rem;
					position: absolute;
					top: 1.05rem;
					left: 16.06rem;
				}
			}
			
		}
		.DrawCash_submit{
			width: 14rem;
			height: 1.96rem;
			margin: 0 auto;
			background: #f94b4b;
			color: white;
			font-size: 0.84rem;
			text-align: center;
			line-height: 1.96rem;
			border-radius: 5px;
		}
		.DrawCash_submit2{
			width: 14rem;
			height: 1.96rem;
			margin: 0 auto;
			background: #e3e3e3;
			color: #191919;
			font-size: 0.84rem;
			text-align: center;
			line-height: 1.96rem;
			border-radius: 5px;
		}
		.warm_warn{
			width: 100%;
			height: 3.56rem;
			padding:0 0.62rem;
			padding-top: 1.25rem;
			color: #595959;
			font-size: 0.75rem;
			line-height: 1.28rem;
			span{
				color: #ff0101;
			}
		}
		.sure_box{			
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 99;
			background: rgba(0,0,0,.45);
			.areUsure{
			width: 14.68rem;
			height: 15.4rem;
			position: absolute;
			top: 16rem;
			left: 5.1rem;
			background: url(../../../assets/tanchuang.png) no-repeat center center;
			background-size: 100%;
			display: flex;
			flex-direction: column;
			.top{
				width: 100%;
				height: 2.03rem;
				text-align: center;
				line-height: 2.03rem;
				margin-top: 4.68rem;
				color: #ffffff;
				font-size: 1rem;
			}
			.center{
				width: 100%;
				height: 6.03rem;
				padding:0 1.25rem;
				padding-top: 0.62rem;
				font-size: 0.78rem;
				color: #666666;
				border-bottom: 1px solid #434343;
				display: flex;
				flex-direction: column;
				justify-content: space-around;
			}
			.choises{
				width: 100%;
				flex: 1;
				display: flex;
				color: #999999;
				div{
					flex: 1;
					height: 100%;
					text-align: center;
					line-height: 2.65rem;
				}
				.yihou{
					border-right: 1px solid #434343;
				}
			}
			
		}	
		
		}

	}
	
</style>


