<template>
	<div class="bind_alipay">
		<div class="alipay_header">
			<div class="back" @click="alipay_back"></div>
			<div class="center">支付宝绑定</div>
			<div class="block"></div>
		</div>
		<div class="ali_number">
			<span>支付宝账号</span>
			<input id="alipayNumber" type="text" placeholder="邮箱地址/手机号码"/>
		</div>
		<div class="ali_name">
			<span>收款人姓名</span>
			<input id="alipayName" type="text" placeholder="收款人姓名"/>
		</div>
		<div class="ali_submint"><span>提交</span></div>
	</div>
</template>

<script>
	export default{
		data(){
			return{}
		},
		methods:{
			alipay_back(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style lang="scss" scoped>
.bind_alipay{
	width: 100%;
	height: 100%;
	background: #f8f8f8;
	.alipay_header{
			  	width: 100%;
	height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 		  .center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 }
 	 .ali_number{
 	 	width: 100%;
 	 	height: 3.46rem;
 	 	padding: 0  0.62rem;
 	 	padding-top: 1.46rem;
 	 	display: flex;
 	 	span{
 	 		display: block;
 	 		width: 4.43rem;
 	 		height: 100%;
 	 		text-align: center;
 	 		line-height:2rem;
 	 		color: #282828;
 	 		font-size: 0.87rem;
 	 	}
 	 	input{
 	 		display: block;
 	 		border: none;
 	 		width: 17.09rem;
 	 		height: 100%;
 	 		border-bottom: 0.03rem solid #c1c1c1;
 	 		background: #f8f8f8;
 	 		margin-left: 0.62rem;
 	 	}
 	 	#alipayNumber::-webkit-input-placeholder{
 	 		color: #d0d0d0;
 	 		font-size: 0.93rem;
 	 	}
 	 }
 	 .ali_name{
 	 	width: 100%;
 	 	height: 3.46rem;
 	 	padding: 0  0.62rem;
 	 	padding-top: 1.46rem;
 	 	display: flex;
 	 	span{
 	 		display: block;
 	 		width: 4.43rem;
 	 		height: 100%;
 	 		text-align: center;
 	 		line-height:2rem;
 	 		color: #282828;
 	 		font-size: 0.87rem;
 	 	}
 	 	input{
 	 		display: block;
 	 		border: none;
 	 		width: 17.09rem;
 	 		height: 100%;
 	 		border-bottom: 0.03rem solid #c1c1c1;
 	 		background: #f8f8f8;
 	 		margin-left: 0.62rem;
 	 	}
 	 	#alipayName::-webkit-input-placeholder{
 	 		color: #d0d0d0;
 	 		font-size: 0.93rem;
 	 	}
 	 }
 	 .ali_submint{
 	 	width: 100%;
 	 	height:2.03rem;
 	 	padding: 0 0.62rem;
 	 	margin-top: 1.87rem;
 	 	span{
 	 		display: block;
 	 		width: 100%;
 	 		height: 100%;
 	 		text-align: center;
 	 		line-height: 2.03rem;
 	 		background: #f94b4b;
 	 		color: #ffffff;
 	 		font-size: 0.84rem;
 	 		border-radius: 5px;
 	 	}
 	 }
}
</style>


