<template>
	<!--验证之前手机号-->
	<div class="verify">
		<div class="verify_header">
			<div class="back" @click="verify_back"></div>
			<div class="center">验证手机号码</div>
			<div class="block"></div>
		</div>
		<div class="verify_warn">换绑手机之前，请输入完整手机号，以验证身份</div>
		<div class="verify_number">
			<input id="verify_number" type="text" :placeholder="data1+id+data2" v-model="verify_number"/>
		</div>
		<div class="next_step" @click="verify_nextStep"><span class="next_step_sp">下一步</span></div>
		<div class="err_num" v-show="isshow">{{err_content}}</div>
	</div>
</template>

<script>



	export default{
		data(){
			return{
				data1:"请输入",
				id:"",
				data2:"的完整手机号",
				verify_number:"",
				isshow:false,
				err_content:"",
				phone_number:''
				
			}
		},
		methods:{
			verify_back(){
					this.$router.go(-1)
			},
			verify_nextStep(){
//				点击进行请求， 判断是否正确，然后是否跳转
			if(this.verify_number==this.phone_number){
				
			this.$router.push("/my/setting/verifyNumber/ChangeBind")
			}else if(this.verify_number == ""){
				this.isshow=true;
				this.err_content = "请输入完整手机号"
				clearInterval(timer);
						var that = this;
						var timer = setInterval(function(){
							that.isshow =false;
						},3000)
						clearInterval();
			}else{
				this.isshow=true;
				this.err_content = "与本机号码不匹配！";
				clearInterval(timer);
						var that = this;
						var timer = setInterval(function(){
							that.isshow =false;
						},3000)
						clearInterval();
			}
			}
		},
		updated(){
			
			if(this.verify_number!==""){
				document.getElementsByClassName("next_step_sp")[0].style.background="#f94b4b";
					 document.getElementsByClassName("next_step_sp")[0].style.color="#f8f8f8";
			}else{
				document.getElementsByClassName("next_step_sp")[0].style.background="#e2e2e2";
					 document.getElementsByClassName("next_step_sp")[0].style.color="#262525";
			}
		},
		beforeMount(){

				//读取缓存登录手机号码
				var phone_number = localStorage.getItem("userId");
				phone_number = JSON.parse(phone_number).phone
				this.phone_number = phone_number;
				console.log(phone_number)

			// 隐藏手机号中间4位		
			var hide_phone_number = phone_number.substr(0,3)+"****"+phone_number.substr(7); 
			this.id = hide_phone_number
		}
	}
</script>

<style lang="scss" scoped>
  .verify{
  	width: 100%;
  	height: 100%;
  	background: #f8f8f8;
		.verify_header{
			  	width: 100%;
	height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 		  .center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 
	}
	.verify_warn{
		width: 100%;
		padding:0 0.62rem;
		height: 2.93rem;
		line-height: 2.93rem;
		color: #f94b4b;
		font-size: 0.84rem;
	}
	.verify_number{
		width: 100%;
		height: 1.87rem;
		padding: 0 0.62rem;
		#verify_number{
			display: block;
			width: 100%;
			height: 100%;
			background: #f8f8f8;
			border: none;
			border-bottom: 0.03rem solid #c1c1c1;
			line-height: 2.33rem;
		}
		#verify_number::-webkit-input-placeholder{
			color: #c6c6c6;
			font-size: 0.75rem;
		}
	}
	.next_step{
		width: 100%;
		height: 2rem;
		padding: 0 0.62rem;
		span{
			display: block;
			width: 100%;
			height: 100%;
			text-align: center;
			line-height: 2rem;
			background: #e2e2e2;
			border-radius: 5px;
			margin-top: 1.87rem;
			color: #262525;
			
		}
	}
	.err_num{
	  position: absolute;
	  top: 9rem;
	  left: 7rem;
	  height: 3.3375rem;
	  line-height: 3.3375rem;
	  text-align: center;
	  font-size: 0.3rem;
	  border-radius: .4125rem;
	  width: 9.5625rem;
	  background: rgba(0, 0, 0, .8);
	  color: #ffffff;
	  
  }
}
</style>


