<template>
    <div class="drawCash">
       <div class="drawCashTop">
            <div class="draw_header">
                <div class="back" @click="back"></div>
                <div class="center">提现</div>
                <div class="block"></div>
            </div>
            <div class="my_balance"> 
                <div class="my_money">我的余额</div>
                <span>{{this.yue}}元</span>
            </div>
            <div class="contact">
                <div class="contact_left">
                    <img src="../../../assets/jilu.png" alt="" @click="txjl">
                    <p>提现记录</p>
                </div>
                <div class="contact_right">
                    <img src="../../../assets/kefu.png" alt="" @click="lxkf">
                    <p>联系客服</p>
                </div>
            </div>
       </div>


 
        <div class="drawCash_main">
            <div class="center">
                <div @click="wxtx"><img src="../../../assets/weixin.png"><span>微信提现<img src="../../../assets/fire.png" style="width: .84375rem;height: .9375rem;"></span></div>
                <div @click="zfbtx"><img src="../../../assets/zhifubao.png"><span>支付宝提现</span></div>
                <div @click="hfcz"><img src="../../../assets/huafei.png"><span>话费充值</span></div>
            </div>
        </div>
        <div class="draw_btm">
            <div>1.提现方式不同，金额到账时间也有差别，到账时间已第三方支付平台操作时间为准，如有疑问，可以联系本公司客服</div>
            <div style="margin-top:1.8125rem;">2.严禁恶意提现，一旦发现，本平台有权拒绝提现申请，并将恶意提现用户视为违规用户，封号处理！</div>
        </div>
        <div class="mubu" v-if="wicket_Contact"></div>
        <div class="kefu" v-if="wicket_Contact">
                <p>选择咨询方式</p>
                <div class="ct">
                    <div class="btn" @click="call" :style="isChange=='call'?change:''">拨打电话</div>
                    <div class="btn" @click="QQ" :style="isChange=='qq'?change:''">企业QQ</div>
                </div>
                <div class="cancel" @click="cancel">取消</div>
                <div class="qeren" @click="qeren">确认</div>
          </div>

    </div>
</template>

<script>
    export default {
        name:"DrawCash",
        data(){
            return{
                //弹窗
                wicket_Contact:false,
                //联系方式
                Contact:'call',
                isChange:'call',
                change:{'border':'1px solid #f94b4b'},
                yue:''
            }
        },
        methods:{
            back(){
				this.$router.push('/my')
            },
            wxtx(){
                this.$router.push('/my/drawCash/weichatDrawCash')
            },
            zfbtx(){
                this.$router.push('/my/drawCash/alipayDrawCash')              
            },
            hfcz(){
            	this.$router.push('/my/drawCash/phoneBill')                 
            },
            txjl(){
                
                this.$router.push('/my/drawCash/drawCashRecord')
            },
            lxkf(){
                this.wicket_Contact = true
                
            },
            call(){
                this.Contact = "call"
                this.isChange = 'call'
            },
            QQ(){
                this.Contact = "QQ"
                this.isChange = 'qq'
            },
            //关闭弹窗
            cancel(){
                this.Contact = ""
                this.wicket_Contact = false
                this.isChange = ''
            },
            //弹窗确认按钮
            qeren(){
                if(this.Contact == "call"){
                    console.log("call")
                    //拨打电话
                    window.location.href = 'tel://17780612157'
                    this.Contact = ""
                    this.isChange = ''
                    this.wicket_Contact = false
                }else if(this.Contact == "QQ"){
                    console.log("QQ")
                    this.Contact = ""
                    this.isChange = ''
                    this.wicket_Contact = false
                }
            }
        },
        beforeMount(){
            this.Contact = ''
            this.wicket_Contact = false
            this.yue = JSON.parse(localStorage.getItem("userId")).balance
        }
    }
</script>

<style lang="scss">
.drawCash{
    height: 100%;
    width: 100%;
    overflow: hidden;
    .mubu{
        position: absolute;left: 0;top: 0;
        height: 100%;width: 100%;background: rgba(0, 0, 0, 0.45);
    }
    .kefu{
        z-index: 999;
        position: absolute;
        height: 11.5625rem;
        width: 12.40625rem;
        text-align: center;
        top: 0;left: 0;right: 0;bottom: 0;
        margin: auto;background: #ffffff;
        border-radius: .5625rem;
        p{font-size: 1.09375rem;line-height: 2.28125rem;color: #f94b4b;}
        .ct{
        height: 6.40625rem;width:100%;border-bottom:.03125rem solid #000;
        .btn{height: 1.3125rem;width: 6.40625rem;font-size: .96875rem;color: #202020;margin: .78125rem 0;
            ;line-height: 1.3125rem;border-radius: .26875rem;display: inline-block;border: 1px solid #a6a6a6;}
        }
        .cancel{display: inline-block;height: 2.875rem;line-height: 2.875rem;color: #f94b4b;font-size: .9375rem;width: 48.5%;
                    border-right: .03125rem solid #000;}
        .qeren{display: inline-block;height: 2.875rem;line-height: 2.875rem;color: #f94b4b;font-size: .9375rem;width: 48.5%;}
    }
    .drawCashTop{
        height: 12.78125rem;
        width: 100%;
        background: url("../../../assets/jianbian.png") no-repeat center center;
        background-size: 23.4375rem 12.78125rem;
            .draw_header{
        width: 100%;
	height: 2.85rem;
        padding-left: 0.62rem;
        padding-right: 0.62rem;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        .back{
            width: 2.62rem;
            height: 100%;
            background: url(../../../assets/back_w.png) no-repeat left center;
            background-size: 0.62rem 1.28rem;
        }
        .center{
            width: 4.61rem;
            height: 2.81rem;
            text-align: center;
            line-height: 2.81rem;
            font-size: 1.12rem;
            color: #ffffff;
           
        }
        .block{
            width: 2.62rem;
        }
    }
    .my_balance{
        width: 100%;
        color: #ffffff;
        .my_money{
            width: 100%;
            text-align: center;
            font-size: 1.125rem;
        }
          span{
            display: inline-block;
            width: 100%;
            text-align: center;
            font-size: 1.375rem;
            margin-top:1.3125rem;
        }
     }
        .contact{
            height: 2.875rem;width: 100%;
            margin-top: 1.5rem;display: flex;
            .contact_left{
                height: 100%;width: 49.9%;border-right: .03125rem solid #ffffff;text-align:center;
                img{height: 1.5rem;width: 1.5rem;margin-top: .15625rem;margin-bottom: .25rem;}
                p{font-size: .84375rem;color: #ffffff;}
            }
            .contact_right{
                height: 100%;width: 50%;text-align:center;
                img{height: 1.5rem;width: 1.5rem;margin-top: .15625rem;margin-bottom: .25rem;}
                p{font-size: .84375rem;color: #ffffff;}
            }
        }

    }

    .drawCash_main{
        height: 14.65625rem;
        width: 100%;
        background: #f8f8f8;
        display: flex;
        overflow: hidden;
        .center{
            height: 12.8125rem;
            width:100%;
            margin-top: .9375rem;
            background: #ffffff;
            
            div{height: 4.25rem;line-height: 4.25rem;border-bottom:.03125rem solid #b2b2b2;padding-left:1.25rem;
                span{float: right;font-size: 1rem;padding-right: .625rem;color:#414141;}
                }
            div:nth-child(1){img{height: 2.09375rem;width: 2.53125rem;}}
            div:nth-child(2){img{height: 2.09375rem;width: 2.09375rem;}}
            div:nth-child(3){border-bottom:none;img{height: 2.1875rem;width: 1.625rem;}}
        }
    }
    .draw_btm{
        height: 100%;
        width: 100%;
        background: #ef811e;
        padding-top: 4.09375rem;
        padding-left:.5rem;
        padding-right: .5rem;
        div{
            width: 100%;
            line-height: 1.3125rem;
            text-align: center;
            font-size: .74875rem;
            color: #ffffff;
        }
    }
}
    
</style>


