<template>
	<div class="form_mission_mian">
	<v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore" bottomPullText="加载中...">
		<div :class="list.surplus!==0?'form_mission_box':'grey'" v-for="list in missionList" :key="list.id" @click="goDownDetail(list)" >
			<div class="form_mission_logo"><img :src="'http://'+list.taskLogo" alt=""></div>
			<div class="form_mission_name">{{list.taskName}}</div>
			<div class="form_mission_surplus">剩余{{list.taskNumber}}份</div>
			<div class="form_mission_money">{{parseInt(list.reward)}}元</div>
		</div>
	 <div v-if="isAll" style="width: 100%;text-align: center;">别拉了，到底了呢~ <img src="../../../../assets/cry.png"/></div>
	</v-loadmore>	
	</div>
</template>

<script>
// 表单注册任务任务列表模块
	import { Loadmore } from 'mint-ui';
	export default{
		name:"FormMessionList",
        components:{
    	  'v-loadmore':Loadmore,
  		  },
		data(){
			return{
				missionList:[],
                pageNo:1,
                pageSize:10,
                allLoaded:false,
                scrollMode:"auto",
                loading:false,
                isAll:false,
			}
		},
		methods:{
			goDownDetail(list){
				if(list.taslNumber == 0){

					return
				}else{
					this.$router.push({path:'/downloadTaskDetail'+"/"+list.id})
				}
			},
            loadBottom:function() {
		      // 上拉加载
		      this.more();// 上拉触发的分页查询
		      this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
		    },
		    loadList(show){
			  this.$http({
						method:'post',
						url:this.$taskSearch,
						 params:{
							 taskType:2,
							 "page.pagesize":this.pageSize,
							 "page.currentPage":this.pageNo
						 },
						  headers: {
							"Content-Type" : "application/json",
							"Accept" : "*/*"
						  },
					}).then((response)=>{
//						console.log(response)
						if(response.data.data.length == 0){
							this.allLoaded = true; 
							this.isAll = show;
						}else{
							this.missionList =this.missionList.concat(response.data.data); 
						}
						
					}).catch((err)=>{
						console.log(err)
					})	
					
		    },
		    more(){
			   	this.pageNo++
			   	this.loadList(true);

		   }
		},
		beforeMount () {
			this.loadList(false)
		}
		
	}
</script>

<style lang="scss" scoped>
	.form_mission_mian{
		position: relative;
		height: 80%;
   		overflow: auto;
		.grey{
			height: 2.53125rem;
			line-height: 2.53125rem;
			padding-left: .875rem;
			margin-bottom: .9375rem;
			display: flex;
			.form_mission_logo{
				height: 2.53125rem;
				width: 2.53125rem;
				img{height: 100%;width: 100%;}
			}
			.form_mission_name{
				position: absolute;
				left: 4.96875rem;
				color: #b8b8b8;
				font-size: .6875rem;
			}
			.form_mission_surplus{
				position: absolute;
				left: 12.53125rem;
				color: #b8b8b8;
				font-size: .6875rem;
			}
			.form_mission_money{
				position: absolute;
				right: .53125rem;
				color: #b8b8b8;
			}
		}
		.form_mission_box{
			height: 2.53125rem;
			line-height: 2.53125rem;
			padding-left: .875rem;
			margin-bottom: .9375rem;
			display: flex;
			.form_mission_logo{
				height: 2.53125rem;
				width: 2.53125rem;
				img{width: 100%;height: 100%;}
			}
			.form_mission_name{
				position: absolute;
				left: 4.96875rem;
				color: #2a2a2a;
				font-size: .6875rem;
			}
			.form_mission_surplus{
				position: absolute;
				left: 12.53125rem;
				color: #2a2a2a;
				font-size: .6875rem;
			}
			.form_mission_money{
				position: absolute;
				right: .53125rem;
				color: #f94b4b;
			}
		}
	}
</style>


