<template>
	<div class="coupon">
		<div class="coupon_header">
			<div class="back" @click="coupon_back"></div>
			<div class="center">卡券</div>
			<div class="block"></div>
		</div>
		<div class="coupon_tab">
			<router-link tag="div" :to="'/my/coupon/discountCoupon'">优惠券</router-link>
			<router-link tag="div" :to="'/my/coupon/freeLesson'">免费课程</router-link>	
		</div>
			<router-view class="view_box"></router-view>
		<div class="to_histiry_cart"><p @click="to_histiry_cart">查看历史卡券<span>＞</span></p></div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				
			}
		},
		methods:{
			coupon_back(){
				this.$router.push("/my")
			},
			to_histiry_cart(){
				this.$router.push('/my/coupon/historyCoupon')
			}
		}
	}
</script>

<style scoped lang="scss">
.router-link-active{
	color: #f94b4b;
}
.coupon{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	position: relative;
	overflow: hidden;
	.to_histiry_cart{
		position: absolute;
		bottom: 1.89rem;
		width: 100%;
		height: 1.78rem;		
		p{
			width: 7.34rem;
			height: 100%;
			margin: 0 auto;
			text-align: center;
			line-height: 1.78rem;
			color: #ffe04a;	
			font-size: 0.87rem;
			span{
				color: #000000;
			}
		}
		}
	.coupon_header{		
			  	width: 100%;
	height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 
		}
		.coupon_tab{
			width: 100%;
			height: 1.62rem;
			background: #fae44c;
			padding: 0 7.4rem;
			display: flex;
			div{
				flex: 1;
				height: 100%;
				text-align: center;
				line-height: 1.62rem;
				font-size: 0.84rem;
			}
		}
		.view_box{
			width: 102%;
