<template>
		<div class="index">
			<!--引导页-->
				<Yindao class="yindao" v-if="isTrue" @to-parent="getChild" ></Yindao>	
				
				<!--首页路由区域-->
				<div class="main" v-if="isTrue1">
					<router-view></router-view>
				</div>
				<!--首页footer-->
					<div class="footer" v-if="isTrue1">
						<router-link :to="'/makemoney'" @click.native="changeImg">							
							<div class="footer_1">
								<img src="../assets/footer_11.png" v-show="isShow"/>
								<img src="../assets/footer_12.png" v-show="!isShow"/>							    
							</div>
							<span :class="[isShow?'noshow':'show']">去赚钱</span>
						</router-link>
						<router-link :to="'/discount'" @click.native="changeImg2">
							<div class="footer_2">
								<img src="../assets/footer_21.png" v-show="isShow2"/>
								<img src="../assets/footer_22.png" v-show="!isShow2"/>							    
							</div>
							<span :class="[isShow2?'noshow':'show']">找优惠</span>						
						</router-link>
						<router-link :to="'/my'" @click.native="changeImg3">
							<div class="footer_3">
								<img src="../assets/footer_31.png" v-show="isShow3"/>
								<img src="../assets/footer_32.png" v-show="!isShow3"/>							    
							</div>
							<span :class="[isShow3?'noshow':'show']">我的</span>
						</router-link>
					</div>
				
		</div>
		 
		 
</template>

<script>
import Yindao from "@/components/YinDao"

export default {
  name: 'index',
  components:{ Yindao },
  data () {
    return {
      isTrue:true,
      isTrue1:false,
      isContent:false,
      title:"产品名称",
      isShow:false,
      isShow2:true,
      isShow3:true
    }
  },
  methods:{
  	getChild(){
		this.isTrue=false;
		this.isTrue1 =true
		this.isContent = true;
		//this.$router.push('/login')
  		
  	},
  	changeImg(){
  		this.isShow= false;
  		this.isShow2= true;
  		this.isShow3= true;
  	},
  	changeImg2(){
  		
  		this.isShow2=false ;
  		this.isShow=true ;
  		this.isShow3=true ;
  		
  	},
  	changeImg3(){
  		this.isShow=true ;
  		this.isShow2=true ;
  		this.isShow3=false 
  	}
  },
  created(){
  	//读取localstorage 如果不是第一次打开app 不显示引导页
      var huancun1 = localStorage.getItem("yindao");
      if (huancun1 == "cun") {
      	  this.isTrue=false;
      	  this.isTrue1 = true
      }else{
      	this.isTrue = true;
      	this.isTrue1 = false
      }
  },
  	beforeMount(){
			//做路由回来之后样式的处理
       	 var name =  this.$route.name;
	  	if(name == "jisu"){
       	  		this.isShow= false;
				this.isShow2= true;
				this.isShow3= true;
       	  };
       	  if (name == "discount") {
				this.isShow2=false ;
				this.isShow=true ;
				this.isShow3=true ;
       	  };
       	  if (name == "my") {
       	  		this.isShow=true ;
				this.isShow2=true ;
				this.isShow3=false 
			 }       
			 
	  
       	  
	  },
	  updated(){
       	 var name =  this.$route.name;
	  	if(name == "jisu"){
       	  		this.isShow= false;
				this.isShow2= true;
				this.isShow3= true;
       	  };
       	  if (name == "discount") {
				this.isShow2=false ;
				this.isShow=true ;
				this.isShow3=true ;
       	  };
       	  if (name == "my") {
       	  		this.isShow=true ;
				this.isShow2=true ;
				this.isShow3=false 
			 }       
			 
	  }
	   

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.yindao{
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 999;
	}

	.index{
		position: relative;
		width: 100%;
		height: 100%;		
		.main{
			width: 100%;
			height: 93%;
			overflow: hidden;
		}
		.footer{
			z-index: 999;
			position: fixed;
			bottom: 0;
			background: #fcbf0e;
			height: 3.06rem;
			width: 100%;
			padding: 0 1.78rem;
			padding-top: 0.59rem;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.show{
						color: #f9704c;
					}
					.noshow{
						color: #5b6064;
					}
			.footer_1,.footer_2,.footer_3{
				display: block;
				width:1.87rem;
				height: 1.21rem;	
					
				img{
					height: 1.21rem;
					margin-left: 0.91rem;					
				}
			}
			span{
				display: block;
				width: 2.87rem;
				height: 0.93rem;
				font-size: 0.62rem;
				text-align: center;
				padding-top: 0.31rem;
				color:#51585e;	
			}
		}
	}

	
	
	
</style>



