<template>
    <div class="free_class">
        <div class="header">
            <div class="back" @click="freeClassBack"></div>
			<div class="center">免费课程</div>
            <div class="block"></div>
        </div>

        <div class="bottom">

            <div class="box" v-for="data in datalist" :key="data.id" @click="goDetail(data)">
                <div class="content">
                    <div class="left">样式</div>
                    <div class="right">
                        <div class="name">免费课程免费课程免费课程免费课程免费课程免费课程免费课程免费课程免费课程免费课程免费课程免费课程免费课程免费课程</div>
                        <div class="feature">课程特点</div>
                        <div class="btm">
                            <div class="logo"></div>
                            <div class="merchant">商家名称</div>
                            <div class="num">12人领取</div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
        
    </div>
</template>

<script>
    export default {
        name:"FreeClass",
        data(){
            return{
                datalist:[{id:1},{id:2},{id:3},{id:4},{id:5},{id:6},{id:7},{id:8},{id:9}]
            }
        },
        methods: {
            freeClassBack(){
                this.$router.push('/discount')
            },
            //点击转第三方
            goDetail(data){
                console.log(data.id)
                this.$router.push({
                    path:'/third',
                    query:{
                        title:"免费课程",
                        url:"http://m.fudaojun.com/yingxiao/common.html?channel=wenjuanxing3"
                    }
                })
            }
        }
    }
</script>

<style lang="scss">
    .free_class{
         	width: 100%;
            height: 100%;
            background: #f8f8f8;      
            display: flex;
            flex-direction: column;
       
        .header{
            width: 100%;
	height: 2.85rem;
            background: #fae44c;
            padding-left: 0.62rem;
            padding-right: 0.62rem;
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            .back{
            width: 2.62rem;
            height: 100%;
            background: url(../../assets/back.png) no-repeat center center;
            background-size: 0.62rem 1.28rem;
                }
                .center{
            width: 7.61rem;
            height: 2.81rem;
            text-align: center;
            line-height: 2.81rem;
            font-size: 1.12rem;
            color: #72463d;
            }
            .block{
                width: 2.62rem;
            }
 	     }

         .bottom{
            height: 100%;
            width: 100%;
            overflow: auto;
            background: #ffffff;
            padding-bottom: 2.0625rem;
            padding-top: .5625rem;
            .box{
                height: 6.59375rem;
                width: 100%;
                background: #ffffff;
                padding: .625rem .625rem 0 .625rem;
                
                .content{
                    height: 100%;width: 100%;
                    display: flex;
                    border-bottom: .03125rem solid #aaaaaa;
                    .left{height: 5.375rem;width: 5.375rem;border: .03125rem solid #959595;
                            margin-right: .25rem;}

                    .right{
                        height: 100%;width: 75%;
                        .name{
                            height: 1.875rem;
                        width: 100%;
                        line-height: .9375rem;
                        font-size: .6875rem;
                        color:#8d8d8d;
                         overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        }
                        .feature{
                        font-size: .8125rem;
                        color:#000000;
                        margin-top: .875rem;
                        margin-bottom: .9375rem;
                        }
                        .btm{height: .84375rem;width: 100%;margin-top: .15625rem;font-size: .71875rem;color:#646464;
                                .logo{height: .78125rem;width: .78125rem;border: .03125rem solid #646464;float: left;}
                                .merchant{float: left;margin-left: .3125rem;}
                                .num{float: right}
                            }

                    }

                }
            }
        }

    }
</style>


